.main-banner {
    background-color: #fff;
    position: relative;

    &__wrapper {
        display: grid;
        grid-template-columns: 1fr 0.5fr;
        grid-template-areas: "title img"
                             "subtitle img"
                             "btn img";
        padding: 100px 0 80px 0;
        margin: 0 1px;
        justify-content: space-between;
        min-height: 730px;
    }

    &__title {
        @include main-page-title;
        color: $black-color;
        margin-bottom: 0;
        margin-top: 0;
        grid-area: title;
    }
    &__subtitle {
        font-size: 40px;
        font-weight: 300;
        line-height: 120%;
        color: $black-color;
        letter-spacing: -0.04em;
        grid-area: subtitle;
        max-width: 600px;
    }
    &__img {
        width: 325px;
        height: 325px;
        background-repeat: no-repeat;
        background-size: contain;
        align-self: center;
        justify-self: end;
        grid-area: img;
    }
    &__btn {
        grid-area: btn;
        display: flex;
        align-self: end;

        span {
            display: inline-block;
            padding: 15px 45px;
            background-color: #d18abd;
            color: #fff;
            max-width: 259px;
            font-size: 18px;
            font-weight: 600;
            line-height: 120%;
            transition: $transition;
            border-radius: 6px;
            border: 1px solid transparent;
    
            &:hover {
                background-color: #af739e;
                box-shadow: $box-shadow;
            }
        }
        
    }
    &__silder-list {
        overflow: hidden;
    }
    &__slide-container {
        width: 100%;
        display: flex;
        justify-content: center;
        padding-bottom: 25px;
    }
    &__slide-control {
        position: relative;

        display: flex;
        column-gap: 15px;

        .swiper-pagination-bullet {
            cursor: pointer;
            width: 15px;
            height: 15px;
            border: 2px solid $black-color;
            background-color: #F8F8F8;
            opacity: 1;
            transition: $transition;

            &.swiper-pagination-bullet-active {
                background-color: $black-color;

                &:hover {
                    border-color: $black-color;
                }
            }

            &:hover {
                border-color: #d18abd;
            }
        }
    }
}
@media(max-width:1199px){
    .main-banner__wrapper {
        padding: 100px 10px 80px 10px;
    }
}
@media(max-width:1024px){
    .main-banner__title {
        font-size: 54px;
        line-height: 115%;
    }
    .main-banner__subtitle {
        font-size: 32px;
        line-height: 115%;
    }
}
@media(max-width:910px){
    .main-banner__title {
        font-size: 42px;
        line-height: 115%;
    }
    .main-banner__wrapper {
        min-height: 630px;
    }
    .main-banner__subtitle {
        font-size: 28px;
        line-height: 115%;
    }
    .main-banner__img {
        width: 290px;
        height: 290px;
        margin-top: 30px;
        align-self: flex-start;
    }
}
@media(max-width:767px){
    .main-banner__wrapper {
        min-height: 530px;
        padding: 30px 0;
        grid-template-rows: repeat(3, auto);
    }
    .main-banner__title {
        font-size: 34px;
    }
    .main-banner__subtitle {
        font-size: 22px;
    }
    .main-banner__btn span {
        padding: 15px 35px;
        font-size: 18px;
    }
    .main-banner__slide-control .swiper-pagination-bullet {
        width: 15px;
        height: 15px;
        border-width: 2px;
    }
    .main-banner__img {
        width: 230px;
        height: 230px;
    }
}
@media(max-width:590px){
    .main-banner__wrapper {
        grid-template-areas: "title title"
                             "subtitle img"
                             "btn img";
        min-height: 430px;
    }
    .main-banner__title {
        font-size: 32px;
    }
    .main-banner__img {
        margin-top: 0;
        width: 195px;
        height: 195px;
    }
    .main-banner__subtitle {
        font-size: 20px;
    }
    .main-banner__btn span {
        font-size: 16px;
    }
}
@media(max-width:480px){
    .main-banner__wrapper {
        grid-template-rows: 0.5fr 1fr 1fr;
        grid-template-areas:  "title title"
                                "subtitle subtitle"
                                "btn img";
        position: relative;
    }
    .main-banner__btn span {
        padding: 15px 25px;
    }
    .main-banner__img {
        position: absolute;
        bottom: 30px;
        width: 145px;
        height: 145px;
    }
    .main-banner__title {
        margin-bottom: 15px;
    }    
}
@media(max-width:450px){
    .main-banner__subtitle {
        font-size: 18px;
    }
    .main-banner__title {
        font-size: 30px;
    }
    .main-banner__btn span {
        font-size: 14px;
        padding: 10px 25px;
    }
}
@media(max-width:380px){
    .main-banner__wrapper {
        min-height: 410px;
    }
    .main-banner__img {
        right: 0;
    }
}