.blockquote {
    padding-top: 65px;
    padding-bottom: 65px;

    .container {
        display: flex;
        justify-content: space-between;
        gap: 32px;
    }
    &__text {
        width: 500px
    }
    &__title {
        @include page-title-two;
        margin: 0;
        margin-bottom: 32px;
    }
    &__text-content {
        font-size: 18px;
        line-height: 120%;
    }
    &__quote {
        width: 500px;

        &-body {
            margin: 0;
            background-color: $purple-color;
            padding: 32px;
            border-radius: 12px;
            color: #fff;
            position: sticky;
            top: 130px;
        }
    }
    &__wrapper {
        display: flex;
        gap: 12px;
        align-items: center;
        margin-bottom: 24px;

        cite {
            font-size: 14px;
            color: #ffffff93;
        }
    }
    &__icon {
        svg {
            width: 30px;
            height: 30px;
        }
    }
}
@media(max-width: 975px){
    .blockquote__title {
        font-size: 34px;
    }
}
@media(max-width: 768px){
    .blockquote__title {
        font-size: 28px;
    }
    .blockquote .container {
        flex-direction: column;
    }
    .blockquote__text {
        width: 100%;
    }
    .blockquote__quote {
        width: 100%;
    }
}
@media(max-width:480px){
    .blockquote__text-content {
        height: 227px;
        &::after {
            bottom: 0;
            background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #fff 100%);
        }
    }
    .blockquote {
        padding: 30px 0;
    }
    .blockquote__title {
        font-size: 26px;
    }
    .blockquote__text-content {
        font-size: 16px;
    }
    .blockquote__quote-body {
        padding: 16px;
    }
}
@media(max-width: 380px){
    .blockquote__title {
        font-size: 24px;
    }
    .blockquote__text-content {
        font-size: 14px;
    }
}