.modal-forgot {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    display: none;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.631372549);
    z-index: 20;

    &__body {
        background-color: #fff;
        padding: 45px 30px 30px;
        width: 860px;
        position: relative;
        border-radius: 12px;
    }
    &__title {
        @include page-title-two;
        margin-bottom: 30px;
    }
    &__subtitle {
        font-size: 18px;
        margin-bottom: 30px;
    }
    &__close {
        width: 40px;
        height: 40px;
        background-image: url("../img/icons/icon-close.png");
        background-repeat: no-repeat;
        background-size: contain;
        right: 10px;
        top: 10px;
        background-position: center;
        position: absolute;
        cursor: pointer;
    }
    &__form {
        display: flex;
        gap: 30px;
    }
    &__input {
        width: 500px;
    }
    &__submit {
        font-size: 18px;
        min-width: 150px;
    }
}
@media(max-width:900px){
    .modal-forgot__body {
        margin: 0 12px;
        width: auto;
    }
    .modal-forgot__input {
        width: 400px;
    }
}
@media(max-width:660px){
    .modal-forgot__form {
        flex-direction: column;
    }
    .modal-forgot__input {
        width: 100%;
        margin-bottom: 0;
    }
}
@media(max-width:480px){
    .modal-forgot__title {
        font-size: 32px;
    }
    .modal-forgot__close {
        width: 30px;
        height: 30px;
    }
}
@media(max-width:390px){
    .modal-forgot__title {
        font-size: 28px;
        margin-bottom: 16px;
    }
    .modal-forgot__subtitle {
        font-size: 16px;
    }
    .modal-forgot__body {
        padding: 45px 15px 30px;
    }
}
@media(max-width:370px){
    .modal-forgot__title {
        font-size: 24px;
    }
    .modal-forgot__subtitle {
        font-size: 14px;
    }
}