.numeric-list {
    padding: 65px 0;

    &__title {
        margin: 0 auto;
        margin-bottom: 64px;
        @include page-title-two;
        max-width: 950px;
        text-align: center;
    }

    &__blocks {
        display: grid;
        grid-template-columns: repeat(4,1fr);
        gap: 20px;
    }

    &__block {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 16px 24px;
        border-radius: 24px;
        border: 1px solid #14141494;
        min-height: 250px;

        &-number {
            font-size: 54px;
            color: $purple-color;
            @include font-alatsi;
        }

        &-text {
            font-size: 18px;
            line-height: 120%;
        }
    }
}
@media(max-width:1024px){
    .numeric-list__blocks {
        grid-template-columns: repeat(3,1fr);
    }
}
@media(max-width:975px){
    .numeric-list__title {
        font-size: 34px;
    }
}
@media(max-width:768px){
    .numeric-list__blocks {
        grid-template-columns: repeat(2,1fr);
    }
    .numeric-list__title {
        font-size: 28px;
        margin-bottom: 32px;
    }
}
@media(max-width:480px){
    .numeric-list__blocks {
        grid-template-columns: 1fr;
    }
    .numeric-list__title {
        font-size: 26px;
    }
    .numeric-list__block {
        min-height: 180px;
    }
    .numeric-list {
        padding: 30px 0;
    }
}
@media(max-width:380px){
    .numeric-list__title {
        font-size: 24px;
    }
}