.useful-info {
    background-color: $grey-color;
    padding-top: 65px;
    padding-bottom: 120px;

    &__title {
        margin: 0;
        margin-bottom: 30px;
        font-size: 38px;
        font-weight: 600;
        color: $black-color;
        text-transform: uppercase;
    }
    &__wrapper {
        display: flex;
        justify-content: space-between;
        column-gap: 15px;
    }
    &__content {
        max-width: 725px;
    }
    &__text {
        font-weight: 400;
        font-size: 21px;
        line-height: 115%;
        color:#535353;
        margin-bottom: 80px;
    }
    &__btn {
        display: inline-block;
        color: #fff;
        font-size: 20px;
        line-height: 115%;
        font-weight: 500;
        padding: 15px 45px;
        cursor: pointer;
    }
    &__img {
        max-height: 320px;
    }
}
@media(max-width: 1199px){
    .useful-info .container {
        margin-top: 28px;
    }
    .useful-info .container {
        width: auto;
    }
}
@media(max-width:1024px){
    .useful-info__content {
        max-width: 525px;
    }
    .useful-info__title {
        font-size: 36px;
    }
}
@media(max-width:967px){
    .useful-info__title {
        font-size: 34px;
    }
    .useful-info__text {
        font-size: 18px;
    }
}
@media(max-width:860px){
    .useful-info__content {
        max-width: 425px;
    }
    .useful-info__title {
        font-size: 32px;
    }
}
@media(max-width:768px){
    .useful-info {
        padding-top: 30px;
        padding-bottom: 100px;
    }

}
@media(max-width:680px){
    .useful-info__wrapper {
        flex-direction: column-reverse;
        row-gap: 15px;
    }
    .useful-info__content {
        max-width: 100%;
    }
    .useful-info__img img {
        max-height: 285px;
        margin: auto;
    }
    .useful-info__text {
        font-size: 16px;
        margin-bottom: 50px;
    }
    .useful-info {
        padding-bottom: 80px;
    }
}
@media(max-width:630px){
    .useful-info__title {
        font-size: 28px;
    }
}
@media(max-width:525px){
    .useful-info__img img {
        max-height: 245px;
    }
    .useful-info__btn {
        font-size: 20px;
    }
    .useful-info__text {
        margin-bottom: 30px;
    }
    .useful-info {
        padding-top: 20px;
        padding-bottom: 50px;
    }
}
@media(max-width: 500px){
    .useful-info__btn {
        width: 100%;
        text-align: center;
    }
    .useful-info__btn {
        font-size: 18px;
        padding: 10px 30px;
    }
}
@media(max-width:480px){
    .useful-info__title {
        font-size: 26px;
    }
}
@media(max-width:380px){
    .useful-info__img img {
        max-height: 225px;
    }
}
@media(max-width:360px){
    .useful-info__title {
        font-size: 24px;
    }
}
@media(max-width:330px){
    .useful-info__title {
        font-size: 22px;
    }
}