@import 'mixins.scss';
@import 'vars.scss';

.footer {
    background-color: $black-color;
    padding: 25px 0;

    &__container {
        display: flex;
        justify-content: space-between;
    }
    &__menu {
        display: flex;
        column-gap: 55px;
        color: #dbdbdb;

        &-link {
            padding: 0;
            font-size: 20px;
            font-weight: 400;
            line-height: 120%;
            letter-spacing: -0.04em;
            margin-bottom: 18px;

            a {
                color: #fff;
                transition: $transition;
                position: relative;

                &::before {
                    content: "";
                    width: 100%;
                    height: 2px;
                    background-color: #fff;
                    position: absolute;
                    left: 0;
                    bottom: -6px;
                    transform: scaleX(0);
                    transition: $transition;
                }
                &:hover::before {
                    transform: scaleX(1);
                }
            }
        }
        &-right {
            margin: 0;
            margin-top: 50px;
        }
        &-left {
            margin: 0;
            margin-top: 50px;
        }
    }
    &__social {
        display: flex;
        column-gap: 30px;

        &-link {
            width: 40px;
            height: 40px;
            background-size: contain;
            transition: .4s;
            &:hover {
                opacity: .8;
            }
        }
    }
    &__right {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    &__copyright {
        padding-top: 15px;
        padding-bottom: 15px;
        text-align: center;
        display: flex;
        flex-direction: column;

        &-link {
            color: #fff;
            font-size: 14px;
            transition: $transition;
            text-decoration: underline;
            text-transform: uppercase;

            &:hover {
                border-color: $pink-color;
                color: $pink-color;
            }
        }
    }
}
@media(max-width:925px){
    .footer__img {
        max-width: 350px;
    }
}
@media(max-width:860px){
    .footer__menu-link {
        font-size: 18px;
        line-height: 110%;
    }
    .footer__copyright {
        padding-top: 20px;
    }
    .footer__right {
        row-gap: 40px;
    }
    .footer__menu-right,
    .footer__menu-left {
        margin-top: 35px;
    }
    .footer__img {
        width: 250px;
    }
    .footer__menu {
        column-gap: 25px;
    }
}
@media(max-width:678px){
    .footer__social-link {
        width: 30px;
        height: 30px;
    }
    .footer__menu-right, .footer__menu-left {
        margin-top: 25px;
    }
}
@media(max-width:625px){
    .footer__container {
        flex-direction: column;
        padding-left: 0;
        row-gap: 20px;
    }
    .footer__right {
        flex-direction: row;
        padding: 0 20px;
    }
    .footer__img {
        align-self: center;
    }
    .footer__social {
        margin-top: 20px;
    }
    .footer__menu-link {
        font-size: 16px;
    }
}
@media(max-width:525px){
    .footer__right {
        flex-direction: column;
    }
    .footer__menu,
    .footer__social {
        justify-content: center;
    }
}
@media(max-width:480px){
    .footer__right {
        row-gap: 20px;
    }
}