.collection {
    &__title {
        @include page-title-one;
    }
    &__list {
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        gap: 15px;

        .load-more {
            grid-column: 1/6;
            justify-content: center;
            margin-top: 50px;
            margin-bottom: 50px;
        }
    }
    &__pokemon {
        border-radius: 6px;
        transition: $transition;
        background-color: $grey-color;
        cursor: pointer;
        min-height: 215px;
        padding: 14px;
        display: flex;
        row-gap: 10px;
        flex-direction: column;
        transition: $transition;
        position: relative;
        border: 1px solid transparent;

        &:hover {
            box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.25);
        }

        &.completed {
            background-color: #fff;
            border-color: rgba(123, 120, 168, 0.568627451);

            .collection__btn {
                background-image: url("../img/icons/pink-galochka.png");
            }
        }
    }
    &__pok-name {
        text-align: center;
        font-size: 18px;
        font-weight: 400;
        text-transform: uppercase;
        color: $black-color;
        cursor: pointer;
        transition: $transition;

    }

    &__pok-img {
        display: flex;
        justify-content: center;
        align-items: center;

        img {
            width: 150px;
            object-fit: contain;
        }
    }
    &__pok-wrapper {
        position: absolute;
    }
    &__btn {
        width: 30px;
        height: 30px;
        background-image: url("../img/icons/black-sparkles.png");
        background-repeat: no-repeat;
        cursor: pointer;
        transition: $transition;
        background-color: transparent;
        background-size: contain;
        border: none;
        box-shadow: none;
        margin-bottom: 0;
        position: relative;
        z-index: 10;

        &.checked {
            background-image: url("../img/icons/pink-galochka.png");
        }
        &:hover {
            background-image: url("../img/icons/pink-sparkles.png");
        }
    }
}
@media(max-width:1199px){
    .collection {
        margin-top: 28px;
    }
}
@media(max-width: 1024px){
    .collection__title {
        font-size: 46px;
        margin-top: 0;
        margin-bottom: 35px;
    }
    .collection__list {
        grid-template-columns: repeat(4,1fr);
    }
    .collection__list .load-more {
        grid-column: 1/5;
    }
}
@media(max-width: 967px){
    .collection__title {
        font-size: 42px;
        margin-bottom: 25px;
    }
    .collection .load-more {
        margin-top: 30px;
    }
}
@media(max-width: 860px){
    .collection__title {
        font-size: 38px;
        margin-bottom: 20px;
    }
    .collection__pok-img img {
        max-width: 140px;
    }
}
@media(max-width:780px){
    .collection__list {
        grid-template-columns: repeat(3,1fr);
    }
    .collection__list .load-more {
        grid-column: 1/4;
    }
}
@media(max-width:720px){

}
@media(max-width: 630px){
    .collection__title {
        font-size: 34px;
    }
    .collection__list {
        grid-template-columns: repeat(2,1fr);
    }
    .collection__list .load-more {
        grid-column: 1/3;
    }
}
@media(max-width: 585px){
    .collection__title {
        font-size: 30px;
    }
}
@media(max-width: 525px){
    .collection .load-more-btn{
        font-size: 20px;
    }
}
@media(max-width:515px){
    .collection__pok-img img {
        max-width: 120px;
    }
    .collection__pok-wrapper {
        margin-top: auto;
    }
}
@media(max-width: 500px){
    .collection .load-more-btn {
        width: 100%;
        text-align: center;
        font-size: 18px;
    }
    .collection .load-more {
        margin-top: 0px;
        margin-bottom: 30px;
    }
}
@media(max-width: 480px){
    .collection__title {
        font-size: 28px;
    }

}
@media(max-width:430px){
    .collection__list {
        grid-template-columns: 1fr;
    }
    .collection__list .load-more {
        grid-column: unset;
    }
    .collection__pok-img img {
        max-width: 162px;
    }
}
@media(max-width: 360px){
    .collection__title {
        font-size: 26px;
    }
}
@media(max-width: 330px){
    .collection__title {
        font-size: 24px;
    }
}