.text-and-img {
    background-color: $grey-color;
    padding-top: 65px;
    padding-bottom: 120px;

    .container {
        display: flex;
        gap: 24px;
    }

    &__title {
        @include page-title-two;
        margin: 0;
        margin-bottom: 24px;
    }

    &__text {
        width: 550px;
    }

    &__content {
        font-size: 18px;
    }

    &__image {
        width: 550px;

        &-container {
            position: sticky;
            top: 135px;
        }
        img {
            border-radius: 12px;
        }
    }
}
@media(max-width:1024px){
    .text-and-img__title {
        font-size: 36px;
    }
}
@media(max-width:967px){
    .text-and-img__title {
        font-size: 32px;
    }
}
@media(max-width:860px){
    .text-and-img__title {
        font-size: 28px;
    }
}
@media(max-width:767px){
    .text-and-img .container {
        flex-direction: column;
    }
    .text-and-img__text {
        width: 100%;
    }
    .text-and-img__image {
        width: 100%;
    }
    .text-and-img__content {
        font-size: 16px;
    }
}
@media(max-width:630px){
    .text-and-img__title {
        font-size: 24px;
    }

}
@media(max-width:480px){
    .text-and-img__content {
        height: 227px;
    }
    .text-and-img__content {
        font-size: 14px;
    }

    .text-and-img {
        padding: 48px 0;
    }
}