.search {
    padding-bottom: 100px;
    &__title {
        @include page-title-one;
    }
    &__input {
        input[type=text] {
            width: 100%;
            height: 45px;
            font-size: 22px;
            border: none;
            padding: 5px;
            padding-left: 10px;
            outline: none;
            background-color: transparent;
            border-bottom: 1px solid $black-color;

            &::placeholder {
                text-transform: uppercase;
                color: #535353;
                font-size: 22px;
            }
            &:focus,
            &:hover {
                border-color: $pink-color;
            }
        }
    }
    &__form {
        position: relative;
        input[type=submit] {
            display: none;
        }
    }
    &__btn-icon {
        width: 35px;
        height: 35px;
        background-image: url("../img/icons/search-icon.png");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        transition: $transition;
        position: absolute;
        right: 0;
        top: 5px;
        cursor: pointer;

        &:hover,
        &:active{
            background-image: url("../img/icons/search-icon-hover.png");
        }
    }

    &__item {
        display: block;
        
        color: $black-color;
        min-height: 180px;
        padding: 30px;
        transition: $transition;
        border-top: 1px solid $black-color;

        &:last-child {
            border-bottom: 1px solid $black-color;
        }

        &-top {
            display: flex;
            justify-content: space-between;
            margin-bottom: 25px;
        }
        &-bottom {
            display: flex;
            column-gap: 15px;
        }
        &-title {
            border-bottom: 1px solid $black-color;
            text-transform: uppercase;
            font-weight: 700;
            font-size: 28px;
        }

        &-text {
            font-size: 20px;
            line-height: 117%;
            font-weight: 300;
        }
        &-date {
            @include font-alatsi;
        }

        &:hover {
            border-color: $pink-color;

            &+ .search__item {
                border-top:1px solid $pink-color;
            }
        }
    }
    & .load-more {
        margin-top: 55px;
    }
}
@media(max-width:1199px){
    .search {
        margin-top: 28px;
    }
}
@media(max-width:1024px){
    .search__title {
        font-size: 46px;
        margin-top: 0;
        margin-bottom: 35px;
    }
    .search__item {
        padding: 25px 0;
    }
}
@media(max-width:967px){
    .search__title {
        font-size: 42px;
        margin-bottom: 25px;
    }
}
@media(max-width:860px){
    .search__title {
        font-size: 38px;
        margin-bottom: 20px;
    }
}
@media(max-width:768px){
    .search {
        margin-top: 15px;
    }
    .search__item-title {
        font-size: 24px;
    }
    .search__item-text {
        max-width: 70%;
    }
    .search__item-text {
        font-size: 18px;
    }
    .search__input input[type=text],
    .search__input input[type=text]::placeholder {
        font-size: 20px;
    }
    .search .load-more {
        margin-top: 35px;
    }
    .search {
        padding-bottom: 80px;
    }
}
@media(max-width:630px){
    .search__title {
        font-size: 34px;
    }
    .search__item-title {
        font-size: 22px;
    }
}
@media(max-width:585px){
    .search__title {
        font-size: 30px;
    }
    .search__item-title {
        font-size: 20px;
    }
    .search__item-text {
        font-size: 16px;
    }
    .search .load-more-btn {
        font-size: 20px;
    }
    .search__input input[type=text],
    .search__input input[type=text]::placeholder {
        font-size: 18px;
    }
}
@media(max-width:520px){
    .search__item-title {
        font-size: 18px;
    }
}
@media(max-width:500px){
    .search .load-more-btn {
        font-size: 18px;
        padding: 10px 30px;
        width: 100%;
        text-align: center;
    }
}
@media(max-width:480px){
    .search__title {
        font-size: 28px;
    }
    .search .filter-tab {
        width: 48%;
    }
    .search__item-text {
        font-size: 14px;
    }
    .search {
        padding-bottom: 50px;
    }
}
@media(max-width:410px){
    .search__item-bottom {
        flex-direction: column;
        row-gap: 10px;
    }
    .search__item-img img {
        max-height: 120px;
        margin: 0 auto;
    }
    .search__item-text {
        max-width: 100%;
    }
    .search__item-text {
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp:3;
        line-clamp:3;
        -webkit-box-orient: vertical;
        box-orient:vertical;
    }
}
@media(max-width:360px){
    .search__title {
        font-size: 26px;
    }
    .search .filter-tab {
        width: 100%;
    }
}
@media(max-width:330px){
    .search__title {
        font-size: 24px;
    }
}

