.counter {
    margin-top: 28px;

    &__title {
        @include page-title-one;
        font-family: 'Raleway';
        line-height: 110%;
        color: $black-color;
        margin-bottom: 50px;
    }
    &__block {
        display: flex;
        column-gap: 40px;
    }
    &__pokemon {
        background-color: #F8F8F8;
        width: 558px;
        min-width: 558px;
        height: 565px;
        display: flex;
        border-radius: 12px;
        flex-direction: column;
        position: relative;

        &.catched {
            height: 450px;
        }
    }
    &__pokemon-img {
        display: flex;
        justify-content: center;
        margin-bottom: 30px;
    }
    &__pokemon-number {
        display: flex;
        justify-content: center;
        align-items: center;
        column-gap: 25px;
        font-size: 48px;
        font-weight: 400;
        color: $black-color;
        text-transform: uppercase;
        font-family: 'Alatsi';
        margin-bottom: 28px;
    }
    &__data {
        width: 100%;
    }
    &__edit {
        width: 31px;
        height: 31px;
        display: inline-block;
        background-image: url("../img/icons/pen.png");
        background-size: contain;
        background-repeat: no-repeat;
        cursor: pointer;
        outline: none;
        border: none;
        background-color: transparent;
        transition: $transition;

        &:hover {
            background-image: url("../img/icons/pen-pink.png");
        }
    }
    &__control {
        display: flex;
        justify-content: center;
        column-gap: 25px;
    }
    &__plus {
        outline: none;
        background-color: #242424;
        border: 3px solid transparent;
        width: 60px;
        height: 60px;
        display: flex;
        justify-content: center;
        align-items: start;
        cursor: pointer;
        transition: $transition;
        border-radius: 6px;

        &::before {
            content: "+";
            font-weight: 400;
            font-size: 48px;
            line-height: 105%;
            text-transform: uppercase;
            font-family: 'Alatsi';
            color: #fff;
        }

        &:hover,
        &:focus {
            border-color: #242424;
            background-color: #fff;

            &::before {
                color: #242424;
            }
        }
    }
    &__minus {
        outline: none;
        background-color: #242424;
        border: 3px solid transparent;
        width: 60px;
        height: 60px;
        display: flex;
        justify-content: center;
        align-items: start;
        cursor: pointer;
        transition: $transition;
        border-radius: 6px;

        &::before {
            content: "-";
            font-weight: 400;
            font-size: 48px;
            line-height: 105%;
            text-transform: uppercase;
            font-family: 'Alatsi';
            color: #fff;
            transition: $transition;
        }

        &:hover,
        &:focus {
            border-color: #242424;
            background-color: #fff;

            &::before {
                color: #242424;
            }
        }
    }
    &__data {
        display: flex;
        flex-direction: column;
        &-list {
            margin-bottom: 20px;

            table {
                width: 100%;
            }
            td {
                padding: 5px 3px;
            }
            td:first-child {
                width: 132px;
                font-weight: 700;
            }
        }
        &-name {
            font-weight: 700;
            font-size: 20px;
            height: 25px;
            color: $black-color;
        }
        &-value {
            font-size: 20px;
            color: $black-color;
            position: relative;

            .counter__edit {
                left: 15%;
                width:20px;
                height: 20px;
                top: 0;
            }
        }
        &-charm {
            position: relative;
            &::before {
                content: "";
                display: block;
                width: 25px;
                height: 25px;
                border: 1px solid $black-color;
                border-radius: 5px;
                cursor: pointer;
            }
            &::after {
                content: "";
                display: none;
                width: 36px;
                height: 30px;
                position: absolute;
                top: -5px;
                left: 2px;
                cursor: pointer;
                background-image: url("../img/icons/pink-galochka.png");
            }
        }
        &-increment span {
            margin-right: 15px;
        }

    }
    &__charm-checkbox {
        display: none;
    }

    &__timer {
        margin-bottom: auto;

        &-info {
            display: flex;
            font-size: 32px;
            color: $black-color;
            font-weight: 700;
            margin-bottom: 25px;

            & span {
                margin-right: 10px;
            }

            .counter__time-hour,
            .counter__time-minute,
            .counter__time-sec {
                margin-right: 0;
            }
        }

        &-controls {
            display: flex;
            column-gap: 25px;
        }
        &-play {
            width: 60px;
            height: 60px;
            background-color: #242424;
            background-image: url("../img/icons/play-white.png");
            background-repeat: no-repeat;
            background-position: center;
            cursor: pointer;
            transition: .4s;
            outline: none;
            border: none;
            border-radius: 6px;

            &:hover {
                background-color: $pink-color;
            }
            &.pause {
                background-image: url("../img/icons/pause_white.png");
            }
        }
        &-reset {
            width: 60px;
            height: 60px;
            background-color: #242424;
            border-radius: 6px;
            background-image: url("../img/icons/icon-reset.png");
            background-repeat: no-repeat;
            background-position: center;
            cursor: pointer;
            transition: $transition;
            outline: none;
            border: none;
            &:hover {
                background-color: $pink-color;
            }
        }
    }

    &__cur-time {
        font-family: 'Alatsi';
        font-weight: 400;
        font-size: 32px;
        color: $black-color;
    }
    &__chance {
        &-name {
            font-weight: 700;
            color: $black-color;
            font-size: 34px;
            letter-spacing: -0.01em;
            text-transform: uppercase;
            margin-right: 30px;
        }
        &-number {
            color: $black-color;
            font-family: 'Alatsi';
            font-size: 34px;
            font-weight: 400;
        }
    }
    &__buttons {
        margin-top: 30px;
        display: flex;
        column-gap: 26px;
        justify-content: space-between;
    }
    &__edit-btn {
        font-size: 16px;
        font-weight: 600;
        color: #fff;
        padding: 20px 13px;
        background-color: $black-color;
        display: flex;
        align-items: center;
        cursor: pointer;
        transition: $transition;
        border: 3px solid transparent;

        &:hover {
            background-color: #fff;
            color: $black-color;
            border-color: $black-color;
        }
    }
    &__shiny-btn {
        font-weight: 400;
        font-size: 24px;
        color: #fff;
        text-transform: uppercase;
        background-color: $black-color;
        display: flex;
        align-items: center;
        column-gap: 10px;
        padding: 11px;
        width: 180px;
        justify-content: space-around;
        cursor: pointer;
        transition: $transition;
        border: 1px solid transparent;
        outline: none;
        border-radius: 6px;


        &::after {
            content: "";
            display: block;
            width: 30px;
            height: 30px;
            background-image: url("../img/icons/white-sparkles.png");
        }

        &:hover {
            background-color: #fff;
            border-color: $pink-color;
            color: $pink-color;

            &::after {
                background-image: url("../img/icons/pink-sparkles.png");
                animation: scale-animation 2s alternate infinite;
            }
        }
    }
    &__delete-btn {
        width: 60px;
        background-color: $black-color;
        background-image: url("../img/icons/trash.png");
        background-repeat: no-repeat;
        background-position: center;
        border-radius: 6px;
        cursor: pointer;
        background-size: 65%;
        transition: $transition;
        outline: none;
        border: none;

        &:hover {
            background-color: $dark-grey;
            background-image: url("../img/icons/trash-black.png");
        }
    }
    &__settings {
        display: none;
        margin-top: 95px;
        width: 100%;

        &-title {
            margin: 0;
            @include page-title-two;
            color: $black-color;
            text-transform: uppercase;
            margin-bottom: 55px;
        }
        &-btn {
            background-color: #d18abd;
            margin: 0 16px 20px;
            padding: 15px;
            font-size: 17px;
            font-weight: 500;
            color: #fff;
            text-transform: uppercase;
            border: none;
            border: 2px solid transparent;

            &.disabled {
                background-color: grey;
                cursor: none;

                &:hover {
                    border-color: transparent;
                    color: #fff;
                }
            }
        }
    }
    &__settings-wrapper {
        display: flex;
    }
    &__settings-tab {
        padding: 16px 0;
        padding-left: 24px;
        border-bottom: 1px solid #bfbfbf;
        border-right: 5px solid #bfbfbf;
        text-transform: uppercase;
        font-weight: 500;
        color: $black-color;
        cursor: pointer;
        transition: $transition;

        &.active {
            background-color: #fff;
            border-right: 5px solid #5b5897;
        }
        &.disable {
            color: grey;
            cursor: auto;

            &:hover {
                border-right:5px solid #bfbfbf;
            }
        }

        &:hover {
            border-right: 5px solid #5b5897;
        }
    }
    &__settings-tabs {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        min-width: 322px;
        border-right: 1px solid #e0e0e0;
    }
    &__settings-body {
        width: 100%;
        padding: 15px 30px 30px 30px;
        display: flex;
        flex-direction: column;
    }
    &__settings-block {
        min-height: 435px;
        background-color: #F8F8F8;
        display: flex;
    }
    &__settings-pokemons {
        display: none;
        grid-template-columns: repeat(4, 1fr);
        gap: 15px;

        &.active {
            display: grid;
        }
    }
    &__search-pokemon {
        display: flex;
        justify-content: space-between;

        form {
            position: relative;
        }
    }
    &__search-icon {
        width: 30px;
        height: 30px;
        background-image: url("../img/icons/search-icon-purple.png");
        background-repeat: no-repeat;
        background-size: contain;
        position: absolute;
        top: 5px;
        right: 15px;
        cursor: pointer;
    }
    &__setting-next {
        height: 45px;
        outline: none;
        border: none;
        padding: 0 30px;
        background-color: #7b78a8;
        color: #fff;
        text-transform: uppercase;
        font-size: 18px;
        font-weight: 500;
        @include font-raleway;
    }
    &__input-search {
        border: none;
        border-bottom: 3px solid #bfbfbf;
        margin-bottom: 25px;
        width: 400px;
        height: 40px;
        background-color: transparent;
        padding: 10px 42px 10px 20px;
        outline: none;
        transition: $transition;

        &::placeholder {
            text-transform: uppercase;
        }
        &:focus,
        &:hover {
            border-color: $purple-color;
        }
    }
    &__games {
        display: none;

        &.active {
            display: grid;
        }
    }
    &__methods {
        display: none;

        &.active {
            display: grid;
        }
    }
    &__other-settings {
        display: none;

        &.active {
            display: grid;
        }
    }
    &__pokemon-item {
        border-radius: 6px;
        display: flex;
        column-gap: 5px;
        justify-content: space-between;
        background-color: #fff;
        padding: 10px;

        cursor: pointer;
        transition: $transition;
        border: 1px solid transparent;

        &:hover {
            box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.25);
        }

        &.active {
            border-color: #7b78a891;
        }
    }
    &__pokemon-info {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    &__pokemon-name {
        text-transform: uppercase;
        font-size: 16px;
        line-height: 117%;
        @include font-raleway;
        color: $black-color;
    }
    &__pokemon-rang {
        @include font-alatsi;
        color: $black-color;
    }
    &__games {
        grid-template-columns: repeat(3, 1fr);
        gap: 10px;
        border-radius: 16px;
        width: 100%;
    }
    &__game {
        background-color: #fff;
        padding: 10px;
        display: flex;
        justify-content: space-between;
        column-gap: 5px;
        border-radius: 6px;
        border: 1px solid transparent;
        cursor: pointer;
        transition: $transition;

        &.active {
            border-color: #7b78a891;
        }
        &:hover {
            box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.25);
        }
    }
    &__game-img img {
        border-radius: 5px;
    }
    &__game-name {
        font-size: 15px;
    }
    &__methods {
        grid-template-columns: repeat(4, 1fr);
        gap: 10px;
    }

    &__method {
        background-color: #fff;
        border-radius: 6px;
        cursor: pointer;
        padding: 15px;
        display: flex;
        text-align: center;
        justify-content: center;
        align-items: center;
        border: 1px solid transparent;
        transition: $transition;
        height: 70px;

        &.active {
            border-color: #7b78a891;
        }
        &:hover {
            box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.25);
        }
    }
    &__other-settings {
        grid-template-columns: repeat(3, 1fr);
        width: 100%;
        gap: 15px;
    }
    &__other-setting {
        display: flex;
        background-color: #fff;
        padding: 15px 20px;
        justify-content: space-between;
        column-gap: 5px;
        align-items: center;
        border-radius: 6px;
        cursor: pointer;
        border: 1px solid transparent;
        transition: $transition;

        &.active {
            border-color: #7b78a891;
        }
        &:hover {
            box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.25);
        }
    }
    &__other-setting-name {
        text-transform: uppercase;
    }
    &__other-setting-icon {
        background-repeat: no-repeat;
        background-size: contain;
        width: 40px;
        height: 40px;
    }
    &__add {
        margin-top: 95px;
        padding: 45px;
        border-radius: 12px;
        width: 100%;
        padding-bottom: 75px;
        background-color: $grey-color;

        &-title {
            text-align: center;
            font-weight: 400;
            font-size: 32px;
            color: #838383;
            margin-bottom: 55px;
        }
        &-btn {
            width: 240px;
            height: 99px;
            border: 3px solid #5f5f5f;
            background-repeat: no-repeat;
            background-position: center;
            cursor: pointer;
            margin: 0 auto;
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;
            transition: $transition;
            border-radius: 6px;

            &::before {
                content: "+";
                font-size: 38px;
                color: #5f5f5f;
                transition: $transition;
            }

            &:hover {
                border-color: $purple-color;

                &::before {
                    color: $purple-color;
                }
            }
        }
            
        
    }
    &__info {
        margin-top: 65px;
        margin-bottom: 120px;

        &-title {
            @include page-title-two;
            text-transform: uppercase;
            margin-bottom: 50px;
            color: $black-color;
        }
        &-descr {
            color: #535353;
            @include main-page-text;
        }
    }
    &__increment-input {
        display: none;
        input[type=text]{
            border: none;
            outline: none;
            border-bottom: 1px solid $black-color;
            font-size: 20px;
            line-height: 115%;
            margin-right: 10px;
            transition: $transition;

            &:focus {
                border-color: $pink-color;
            }
        }
    }
    &__increment-btn {
        outline: none;
        border: none;
        padding: 5px 10px;
        color: #fff;
        background-color: $pink-color;
        text-transform: uppercase;
        cursor: pointer;
        border-radius: 6px;
        transition: $transition;
        

        &:hover {
            box-shadow: 0 3px 5px 0 rgb(127 127 127 / 25%);
        }
    }
    &__hunt-count {
        margin-left: 55px;
    }
    &__hunt-input {
        display: none;
        justify-content: center;
        column-gap: 10px;
        margin-bottom: 28px;
    }
    &__hunt-value {
        border: none;
        outline: none;
        border-bottom: 1px solid $black-color;
        transition: $transition;
        background-color: transparent;
        font-size: 48px;
        @include font-alatsi;
        max-width: 200px;
        margin-left: 100px;
        text-align: center;
        transition: $transition;

        &:focus {
            border-color: $pink-color;
        }
    }
    &__hunt-btn {
        outline: none;
        border: none;
        background-color: $pink-color;
        color: #fff;
        text-transform: uppercase;
        align-self: end;
        cursor: pointer;
        border-radius: 6px;
        padding: 5px 10px;
        
        transition: $transition;

        &:hover {
            box-shadow: 0 3px 5px 0 rgba(127, 127, 127, 0.25);
        }
    }
    &__btn-next {
        margin-top: auto;
        align-self: end;
        border: none;
        outline: none;
        background-color: $pink-color;
        font-size: 16px;
        font-weight: 400;
        text-transform: uppercase;
        
        transition: $transition;
        color: #fff;
        padding: 10px 25px;
        border: 1px solid transparent;
        cursor: pointer;

        &:hover {
            box-shadow: 0 3px 5px 0 rgba(127, 127, 127, 0.25);
        }
    }
    &__settings-list {
        max-height: 295px;
        overflow-y: auto;
        padding: 10px 5px;

        &::-webkit-scrollbar {
            width: 5px;
        }
        &::-webkit-scrollbar-thumb {
            background-color: $purple-color;
            border-radius: 4px;
        }
    }
    &__set-and-add {
        display: flex;
    }
    &__result {
        display: none;
        height: 100%;
        flex-direction: column;
    }
    &__result-data {
        table {
            width: 100%;
        }
        td {
            padding: 5px 3px;
        }
        td:first-child {
            font-weight: 700;
            width: 175px;
        }

    }
    &__result-wrapper {
        display: flex;
        flex-direction: column;
        row-gap: 10px;
    }
    &__result-title {
        @include page-title-three;
        margin-bottom: 20px;
    }
    &__result-btns {
        display: flex;
        justify-content: space-between;
        margin-top: auto;
    }
    &__result-finish {
        width: 45px;
        height: 45px;
        background-image: url("../img/icons/white-sparkles.png");
        background-repeat: no-repeat;
        background-position: center;
        margin-bottom: 0;
        border: 2px solid transparent;
        transition: $transition;

        &:hover {
            background-image: url("../img/icons/pink-sparkles.png");
        }
    }
    &__result-prev {
        margin-bottom: 0;
        font-size: 18px;
        font-weight: 500;
    }
}

.count-hunts {
    margin-top: 95px;

    &__title {
        @include page-title-two;
        text-transform: uppercase;
        color: $black-color;
        margin-bottom: 50px;
    }
    &__list {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        column-gap: 30px;
        margin-bottom: 60px;
    }
    &__item {
        background-color: $grey-color;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 25px;
        border-radius: 12px;
    }
    &__item-img {
        width: 250px;
        height: 280px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        margin-bottom: 30px;
    }
    &__item-name {
        text-transform: uppercase;
        font-weight: 400;
        color: $black-color;
        font-size: 24px;
        margin-bottom: 10px;
    }
    &__item-number {
        @include font-alatsi;
        font-size: 36px;
        text-transform: uppercase;
        color: $black-color;
    }
    &__button {
        font-size: 18px;
        color: #fff;
        font-weight: 500;
        background-color: $black-color;
        padding: 20px 30px;
        margin: 0 auto;
        cursor: pointer;
    }
    &__all {
        display: flex;
    }
}
.counter__charm-checkbox:checked + label::after {
    display: block;
}
@media(max-width:1199px){
    .counter {
        margin-top: 28px;
    }
    .counter__chance-name,
    .counter__chance-number,
    .counter__timer-info,
    .counter__cur-time {
        font-size: 28px;
    }
    .count-hunts__item-name {
        font-size: 18px;
    }
    .count-hunts__item-number {
        font-size: 30px;
    }
    .counter__settings-pokemons,
    .counter__methods {
        width: 100%;
        grid-template-columns: repeat(3, 1fr);
    }
    .counter__input-search {
        width: 320px;
    }
}
@media(max-width:1080px){
    .counter__games {
        grid-template-columns: repeat(2, 1fr);
    }
}
@media(max-width:1060px){
    .counter__input-search {
        width: 100%;
    }
    .counter__search-pokemon {
        flex-direction: column;
    }
    .counter__other-settings {
        grid-template-columns: 1fr 1fr;
    }
}
@media(max-width:1024px){
    .counter__pokemon {
        min-width: 458px;
    }
    .count-hunts__item-img {
        width: 100%;
    }
    .counter__title {
        font-size: 46px;
        margin-top: 0;
        margin-bottom: 35px;
    }
    .counter__settings-title,
    .count-hunts__title,
    .counter__info-title {
        font-size: 36px;
    }
}
@media(max-width:997px){
    .counter__settings-tabs {
        min-width: 285px;
    }
}
@media(max-width:967px){

    .counter__block {
        column-gap: 30px;
    }
    .counter__pokemon {
        min-width: 418px;
    }

    .count-hunts__item-img {
        height: 200px;
    }
    .counter__hunt-input {
        flex-direction: column;
        row-gap: 15px;
    }
    .counter__hunt-btn {
        align-self: center;
    }
    .counter__hunt-value {
        margin-left: 0;
        align-self: center;
    }
    .counter__pokemon {
        height: auto;
        padding-bottom: 20px;
    }
    .counter__title {
        font-size: 42px;
