.personal-acc {
    padding-bottom: 120px;

    &__title {
        @include page-title-one;
    }
    &__history {
        width: 100%;
        display: none;
        &__title {
            margin: 0;
            margin-bottom: 30px;
            font-size: 32px;
            font-weight: 600;
        }
    }
    &__container {
        display: flex;
        flex-direction: column;
    }
    &__sidebar {
        display: flex;
        flex-wrap: wrap;
        gap: 24px;
        width: 100%;
        margin-bottom: 64px;

        &-btns {
            font-size: 24px;
            font-weight: 300;
            padding: 20px 10px;
            border: 1px solid #ABABAB;
            background-color: #F8F8F8;
            border-radius: 12px;
            cursor: pointer;
            transition: .4s;
            width: 300px;
            text-align: center;

            &.active {
                background-color: $black-color;
                color: #fff;
            }

            &:hover {
                color: $pink-color;
            }
        }
    }
    &__settings {
        display: flex;
        flex-direction: column;
        min-height: 500px;
        width: 645px;
    }
    &__settings-title {
        margin: 0;
        margin-bottom: 30px;
        font-size: 38px;
        font-weight: 600;
    }

    &__change-pass {
        display: none;
        form {
            display: flex;
            flex-direction: column;
            min-height: 500px;
        }
    }
    &__wrapper {
        margin-bottom: 50px;
    }
    &__info {
        font-size: 24px;
        margin-bottom: 20px;
        font-weight: 700;
    }
    &__value {
        font-size: 28px;
        font-weight: 300;
        margin-bottom: 20px;
    }
    &__btns {
        margin-top: auto;
        display: flex;
        justify-content: space-between;
        column-gap: 30px;
    }
    &__pass-btn {
        border: 1px solid #d18abda2;
        color: $pink-color;
        background-color: #fff;
        transition: .4s;
        padding: 15px 30px;
        font-size: 18px;
        font-weight: 500;
        cursor: pointer;
        border-radius: 6px;

        &:hover {
            border-color: $pink-color;
        }
    }
    &__btn-upd {
        padding: 15px 30px;
        margin-bottom: 0;
        font-size: 18px;
        font-weight: 500;
    }
    &__update {
        display: none;
        form {
            display: flex;
            flex-direction: column;
            min-height: 500px;
        }
    }
    &__input {
        border: none;
        outline: none;
        background-color: transparent;
        border-bottom: 1px solid #ABABAB;
        font-size: 20px;
        padding: 10px 5px;
        margin-bottom: 20px;
        font-weight: 300;

        &::placeholder {
            font-weight: 200;
            font-size: 20px;
        }
    }
    &__submit {
        margin-top: auto;
        font-size: 20px;
        text-transform: uppercase;
        padding: 15px 30px;
    }
    &__page {
        width: 100%;
        display: flex;
    }
}
.active-hunts {
    margin-bottom: 50px;
    &__list {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 25px;
    }

    &__pokemon {
        transition: $transition;
        background-color: $grey-color;
        cursor: pointer;
        min-height: 215px;
        padding: 14px;
        display: flex;
        row-gap: 10px;
        flex-direction: column;
        transition: $transition;
        position: relative;
        border-radius: 6px;

        &:hover {
            box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.25);
        }

        &-img {
            display: flex;
            justify-content: center;
            align-items: center;
            img {
                width: 150px;
            }
        }
        &-name {
            text-align: center;
            font-size: 20px;
            font-weight: 400;
            text-transform: uppercase;
            color: $black-color;
            cursor: pointer;
            transition: $transition;
    
            span {
                border-bottom: 1px solid $black-color;
            }
        }
        &-count {
            font-weight: 700;
            text-align: center;
            font-size: 24px;
            font-family: 'Alatsi';
            margin-bottom: 20px;
        }
        &-link {
            text-transform: uppercase;
            display: flex;
            justify-content: end;
            column-gap: 10px;
            
            a {
                color: $black-color;
                border-bottom: 1px solid $black-color;
                font-size: 14px;
            }
            &::after {
                content: "";
                display: block;
                width: 20px;
                height: 20px;
                background-image: url("../img/icons/next-arrow.png");
                background-repeat: no-repeat;
                background-size: contain;
            }
        }
    }
}
.find-shiny {
    &__list {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 25px;

        .load-more {
            grid-column: 1/4;
            justify-content: center;
            margin-top: 50px;
            margin-bottom: 50px;
        }
    }
    &__pokemon {
        border-radius: 6px;
        transition: $transition;
        background-color: $grey-color;
        cursor: pointer;
        min-height: 215px;
        padding: 14px;
        display: flex;
        row-gap: 10px;
        flex-direction: column;
        transition: $transition;
        position: relative;

        &:hover {
            box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.25);
        }

        &-img {
            display: flex;
            justify-content: center;
            align-items: center;
            img {
                width: 150px;
            }
        }
        &-name {
            text-align: center;
            font-size: 20px;
            font-weight: 400;
            text-transform: uppercase;
            color: $black-color;
            cursor: pointer;
            transition: $transition;
            margin-bottom: 10px;
    
            span {
                border-bottom: 1px solid $black-color;
            }
        }

    }
    &__wrapper {
        display: flex;
        column-gap: 10px;
    }
    &__name {
        text-transform: uppercase;
        margin-bottom: 5px;
        min-width: 82px;
    }
    &__value {
        font-weight: 700;
        text-transform: uppercase;

        &.count-numb,
        &.hunt-chance {
            font-family: 'Alatsi';
        }
    }
    &__del {
        width: 35px;
        height: 35px;
        background-color: $black-color;
        background-image: url("../img/icons/trash.png");
        background-repeat: no-repeat;
        background-position: center;
        cursor: pointer;
        background-size: 65%;
        transition: $transition;
        outline: none;
        border: none;
        align-self: end;
        border-radius: 6px;

        &:hover {
            background-color: $dark-grey;
            background-image: url("../img/icons/trash-black.png");
        }
    }
}
@media(max-width: 1199px){
    .personal-acc__sidebar-btns {
        font-size: 20px;
    }
    .active-hunts__list,
    .find-shiny__list {
        grid-template-columns: repeat(2,1fr);
        gap: 15px;
    }
    .find-shiny__list .load-more {
        grid-column: 1/3;
    }
}
@media(max-width: 1024px){
    .personal-acc__title {
        font-size: 46px;
        margin-bottom: 35px;
    }
}
@media(max-width:991px){
    .personal-acc__btn-upd,
    .personal-acc__pass-btn,
    .personal-acc__submit {
        font-size: 18px;
        padding: 10px 20px;
    }
    .personal-acc__btns {
        column-gap: 10px;
    }
    .personal-acc__settings {
        width: 100%;
    }
}
@media(max-width: 967px){
    .personal-acc__title {
        font-size: 42px;
        margin-bottom: 25px;
    }
}

@media(max-width: 860px){
    .personal-acc__title {
        font-size: 38px;
        margin-bottom: 20px;
    }
    .personal-acc__settings-title {
        font-size: 26px;
        margin-bottom: 25px;
    }
    .personal-acc__wrapper {
        margin-bottom: 20px;
    }
    .personal-acc__info {
        font-size: 20px;
        margin-bottom: 15px;
    }
    .personal-acc__value,
    .personal-acc__input {
        font-size: 26px;
        margin-bottom: 20px;
    }
    .personal-acc__input::placeholder{
        font-size: 26px;
    }

    .personal-acc__btn-upd, .personal-acc__pass-btn {
        font-size: 16px;
    }
    .personal-acc__sidebar {
        margin-right: 30px;
    }
}
@media(max-width:767px){
    .personal-acc__btns {
        flex-direction: column;
        row-gap: 10px;
    }


    .personal-acc__sidebar-btns {
        width: calc(50% - 12px);
    }
}
@media(max-width:710px){
    .personal-acc__container {
        flex-direction: column;
    }
    .personal-acc__sidebar {
        margin-bottom: 20px;
    }
    .personal-acc__input::placeholder{
        font-size: 20px;
    }
    .personal-acc__settings,
    .personal-acc__update form,
    .personal-acc__change-pass form {
        min-height: unset;
    }
}
@media(max-width: 630px){
    .personal-acc__title {
        font-size: 34px;
    }
}
@media(max-width: 585px){
    .personal-acc__title {
        font-size: 30px;
    }
    .active-hunts__list,
    .find-shiny__list {
        grid-template-columns: 1fr;
        gap: 15px;
    }
    .find-shiny__list .load-more {
        grid-column: 1/2;
    }
}
@media(max-width: 480px){
    .personal-acc__title {
        font-size: 28px;
    }
    .personal-acc__sidebar-btns {
        width: 100%;
    }
    .personal-acc__sidebar {
        gap: 12px;
    }
}
@media(max-width: 360px){
    .personal-acc__title {
        font-size: 26px;
    }
}
@media(max-width: 330px){
    .personal-acc__title {
        font-size: 24px;
    }
}