.videos {
    background-color: $black-color;
    padding: 65px 0;

    &__title {
        margin: 0;
        margin-bottom: 24px;
        color: #fff;
        @include page-title-two;

        span {
            color: $pink-color;
        }
    }

    &__descr {
        color: #fff;
        font-size: 18px;
        margin-bottom: 32px;
    }

    &__slider {
        overflow-x: hidden;
    }

    &__slide {
        position: relative;
        cursor: pointer;

        &::after {
            content: "";
            background-image: url("../img/icons/play-white.png");
            background-repeat: no-repeat;
            background-size: contain;
            width: 50px;
            height: 50px;
            display: block;
            position: absolute;
            top: calc(50% - 25px);
            right: calc(50% - 25px);
            transition: .4s;
        }

        &-iframe {
            display: none;
        }

        &-preview {
            img {
                border-radius: 12px;
                border: 1px solid #fff;
                object-fit: cover;
            }
        }
    }
    &__list {
        display: none;
    }
    &__head {
        display: flex;
        justify-content: space-between;
        gap: 24px;
    }
    &__controls {
        display: flex;
        align-items: center;
        gap: 8px;
    }
}
@media(max-width:975px){
    .videos__title {
        font-size: 34px;
    }
}
@media(max-width: 768px){
    .videos__title {
        font-size: 28px;
    }
    .videos__descr {
        font-size: 16px;
    }
}
@media(max-width:620px){
    .videos__head {
        flex-direction: column;
    }
    .videos__controls {
        justify-content: end;
    }
    .videos__descr {
        margin-bottom: 0;
    }
}
@media(max-width: 480px){
    .videos__title {
        font-size: 26px;
    }
    .videos {
        padding: 30px 0;
    }
}
@media(max-width: 380px){
    .videos__title {
        font-size: 24px;
    }
}