.about-shiny {
    background-color: $black-color;

    .container {
        padding: 65px 0 120px 0;
    }

    &__title {
        @include page-title-two;
        text-transform: uppercase;
        color: #fff;
        margin: 0;
        margin-bottom: 45px;
    }
    &__wrapper {
        display: flex;
        justify-content: space-between;
    }
    &__text {
        @include main-page-text;
        color: rgba(255, 255, 255, 0.83);
        max-width: 650px;
    }
    &__image {
        min-width: 325px;
    }
}
@media(max-width:1024px){
    .about-shiny__wrapper {
        column-gap: 10px;
    }
    .about-shiny__image img{
        margin-left: auto;
    }
    .about-shiny__text {
        font-size: 22px;
    }
}
@media(max-width:975px){
    .about-shiny__title {
        font-size: 34px;
        margin-bottom: 25px;
    }
}
@media(max-width:889px){
    .about-shiny__image {
        min-width: 260px;
    }
}
@media(max-width:768px){
    .about-shiny__image {
        min-width: 210px;
    }
    .about-shiny__title {
        font-size: 28px;
    }
    .about-shiny__text {
        font-size: 18px;
    }
}
@media(max-width:678px){
    .about-shiny__title {
        margin-bottom: 15px;
    }
}
@media(max-width:625px){
    .about-shiny__image {
        min-width: 150px;
    }
    .about-shiny__text {
        font-size: 16px;
    }
    .about-shiny__title {
        font-size: 26px;
    }
}
@media(max-width:515px){
    .about-shiny__wrapper {
        flex-direction: column;
    }
    .about-shiny__image img {
        margin: 0 auto;
    }
    .about-shiny__image {
        width: 150px;
        margin: 0 auto;
        margin-top: 15px;
    }
}
@media(max-width:480px){
    .about-shiny__title {
        font-size: 24px;
    }
    .about-shiny__text {
        font-size: 14px;
        line-height: 115%;
    }
}
