.modal-reg {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    display: none;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.631372549);
    z-index: 20;

    &__body {
        background-color: #fff;
        padding: 45px 30px 30px;
        width: 860px;
        position: relative;
        border-radius: 12px;
    }
    &__title {
        @include page-title-two;
        margin-bottom: 30px;
    }
    &__close {
        width: 40px;
        height: 40px;
        background-image: url("../img/icons/icon-close.png");
        background-repeat: no-repeat;
        background-size: contain;
        right: 10px;
        top: 10px;
        background-position: center;
        position: absolute;
        cursor: pointer;
    }
    &__form-wrapper {
        display: flex;
        justify-content: space-between;
        column-gap: 30px;

        .modal-form-input {
            width: calc(50% - 15px);
        }
    }
    &__agreement {
        margin-top: 30px;
    }
    &__agr-check {
        display: none;
    }
    &__agreement {
        label {
            position: relative;
            padding-left: 45px;
            height: 30px;
            display: flex;
            align-items: center;
            margin-bottom: 35px;
            &::before {
                content: "";
                width: 30px;
                height: 30px;
                display: inline-block;
                border-radius: 6px;
                background-color: transparent;
                border: 1px solid #abababa1;
                position: absolute;
                left: 0;
                cursor: pointer;
            }
            &::after {
                content: "";
                display: none;
                width: 46px;
                height: 40px;
                background-image: url("../img/icons/pink-galochka.png");
                background-repeat: no-repeat;
                background-size: contain;
                position: absolute;
                left: 2px;
                top: -8px;
                cursor: pointer;
            }
    
            span {
                font-weight: 500;
            }
        }
        
    }

    &__submit {
        border: none;
        outline: none;
        padding: 15px 45px;
        font-size: 18px;
        font-weight: 400;
        color: #fff;
        transition: .4s;
        cursor: pointer;
    }
}
.modal-reg__agr-check:checked + label::after {
    display: block;
}
.modal-form-input {
    border: none;
    outline: none;
    background-color: transparent;
    border-bottom: 1px solid #ABABAB;
    font-size: 18px;
    padding: 10px 5px;
    margin-bottom: 20px;
    font-weight: 300;
    transition: .4s;

    &::placeholder {
        font-weight: 200;
        font-size: 18px;
    }
    &:focus {
        border-color: $pink-color;
    }
}
@media(max-width:900px){
    .modal-reg__body {
        margin: 0 12px;
    }
}
@media(max-width:620px){
    .modal-reg__form-wrapper {
        flex-direction: column;
    }
    .modal-reg__form-wrapper .modal-form-input {
        width: 100%;
    }
}
@media(max-width:480px){
    .modal-reg__title {
        font-size: 32px;
    }
    .modal-reg__close {
        width: 30px;
        height: 30px;
    }
}
@media(max-width:390px){
    .modal-reg__title {
        font-size: 28px;
    }
    .modal-reg__body {
        padding: 45px 15px 30px;
    }
    .modal-reg__agreement label span {
        font-size: 14px;
    }
}
@media(max-width:370px){
    .modal-reg__title {
        font-size: 24px;
    }
}