.filter-tabs {
    display: flex;
    flex-wrap: wrap;
    row-gap: 10px;
    column-gap: 10px;
    margin-top: 40px;
    margin-bottom: 40px;
}
.filter-tab {
    padding: 10px 30px;
    font-size: 14px;
    line-height: 110%;
    text-transform: uppercase;
    background-color: $dark-grey;
    color: $black-color;
    transition: $transition;
    min-width: 140px;
    text-align: center;
    cursor: pointer;
    border: 1px solid transparent;
    border-radius: 6px;

    &.active {
        background-color: $black-color;
        color: #fff;
    }

    &:hover {
        background-color: #fff;
        border-color: $black-color;
        color: $black-color;
    }
}
@media(max-width:768px){
    .filter-tabs {
        margin-top: 30px;
        margin-bottom: 30px;
    }
    .filter-tab {
        padding: 10px 20px;
    }
}
@media(max-width:480px){
    .filter-tab {
        width: 48%;
    }
}
@media(max-width:400px){
    .filter-tabs {
        margin-top: 0;
    }
    .filter-tab {
        width: 100%;
    }
}
