.breadcrumbs {
    padding-top: 28px;
    display: flex;

    span {
        text-transform: uppercase;
        font-size: 12px;
        font-weight: 700;
        margin-right: 5px;
        color: #616161;


        a {
            color: $black-color;
            display: flex;
            align-items: center;
            column-gap: 5px;

            &::after {
                content: "";
                width: 15px;
                height: 11px;
                display: block;
                background-image: url("../img/icons/breadcrumb.png");
                background-size: contain;
                background-repeat: no-repeat;
            }
        }

        &:last-child {
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp:1;
            line-clamp:1;
            -webkit-box-orient: vertical;
            box-orient:vertical;
        }
    }
}
@media(max-width:1199px){
    .breadcrumbs {
        padding-top: 28px;
    }
}