.useful-page {
    margin-top: 28px;
    padding-bottom: 75px;

    &__title {
        @include page-title-one;
        margin: 0;
        margin-bottom: 50px;
    }
    &__descr {
        font-size: 18px;
        line-height: 120%;
        margin-bottom: 32px;
    }
    &__subtitle {
        @include page-title-two;
        margin: 0;
        margin-bottom: 24px;
    }
    &__articles-descr {
        font-size: 18px;
        line-height: 120%;
        margin-bottom: 24px;
    }
    &__articles-list {
        display: grid;
        grid-template-columns: repeat(4,1fr);
        gap: 20px;

        .article:first-child {
            grid-column: 1/3;
        }
    }
    &__articles-link {
        display: flex;
        justify-content: center;
        margin: 38px 0;
        
        a {
            display: inline-block;
            min-width: 150px;
            font-size: 18px;
            text-align: center;
        }
    }
}
@media(max-width:1024px){
    .useful-page__title {
        font-size: 46px;
        margin-bottom: 35px;
    }
    .useful-page__subtitle {
        font-size: 36px;
    }
}
@media(max-width:992px){
    .useful-page__articles-list {
        grid-template-columns: repeat(3, 1fr);
    }
    .useful-page__articles-list .article:last-child {
        grid-column: 2/4;
    }
}
@media(max-width:967px){
    .useful-page__title {
        font-size: 42px;
    }
    .useful-page__subtitle {
        font-size: 32px;
    }
}
@media(max-width:860px){
    .useful-page__title {
        font-size: 38px;
        margin-bottom: 20px;
    }
    .useful-page__subtitle {
        font-size: 28px;
    }
}
@media(max-width:767px){
    .useful-page__articles-list {
        grid-template-columns: repeat(2, 1fr);
    }
    .useful-page__articles-list .article:last-child {
        grid-column: unset;
    }
    .useful-page {
        padding-bottom: 25px;
    }
}
@media(max-width:630px){
    .useful-page__title {
        font-size: 34px;
    }
    .useful-page__subtitle {
        font-size: 24px;
    }
    .useful-page__descr,
    .useful-page__articles-descr {
        font-size: 16px;
    }
}
@media(max-width:585px){
    .useful-page__title {
        font-size: 30px;
    }
}
@media(max-width:480px){
    .useful-page__title {
        font-size: 28px;
    }
    .useful-page__descr,
    .useful-page__articles-descr {
        font-size: 14px;
    }
    .useful-page__articles-list {
        grid-template-columns: 1fr;
    }
    .useful-page__articles-list .article:first-child {
        grid-column: unset;
    }
    .useful-page {
        padding-bottom: 0;
    }
}
