.about-us {
    background-color: #F8F8F8;

    &__wrapper {
        padding: 65px 0 120px 0;
        display: flex;
        column-gap: 90px;
    }

    &__title {
        @include page-title-two;
        color: $black-color;
        margin: 0;
        margin-bottom: 45px;

        span {
            color: #d18abd;
        }
    }

    &__info {
        max-width: 650px;
    }

    &__image {
        display: flex;

        img {
            object-fit: contain;
        }
    }

    &__text {
        @include main-page-text;
        color: $black-color;
    }
}
@media(max-width:1199px){
    .about-us__wrapper {
        column-gap: 40px;
    }
}
@media(max-width:1024px){
    .about-us__wrapper {
        padding: 55px 40px;
    }
    .about-us__image {
        min-width: 250px;
    }
    .about-us__text {
        font-size: 22px;
    }
}
@media(max-width: 975px){
    .about-us__title {
        font-size: 34px;
        margin-bottom: 25px;
    }
}
@media(max-width:850px){
    .about-us__image {
        min-width: 210px;
    }
}
@media(max-width:768px){
    .about-us__image {
        min-width: 150px;
    }
    .about-us__title {
        font-size: 28px;
    }
    .about-us__text {
        font-size: 18px;
    }
}
@media(max-width:678px){
    .about-us__wrapper {
        padding: 45px 30px;
        column-gap: 20px;
    }
    .about-us__title {
        margin-bottom: 15px;
    }
    .about-us__image {
        min-width: 120px;
    }
}
@media(max-width:525px){
    .about-us__wrapper {
        flex-direction: column-reverse;
    }
    .about-us__image {
        display: none;
    }
}
@media(max-width:480px){
    .about-us__wrapper {
        padding: 30px 0;
    }
    .about-us__title {
        font-size: 26px;
    }
    .about-us__text {
        font-size: 16px;
    }
}
@media(max-width:380px){
    .about-us__title {
        font-size: 24px;
        margin-bottom: 20px;
    }
    .about-us__text {
        font-size: 14px;
    }
}