@import 'mixins.scss';
@import 'vars.scss';

* {
    box-sizing: border-box;
}
body {
    margin: 0;
    padding: 0;
    @include font-raleway;
    color: $black-color;
    box-sizing: border-box;
    background-color: #fff;
}

.container {
    width: 1148px;
    margin: 0 auto;
}

p {
    margin: 0;
    margin-bottom: 15px;
}
  
img {
    display: block;
    max-width: 100%;
    max-height: 100%;
}
main {
    margin-top: 116px;
}
ul,
ol,
li {
    list-style: none;
}
ul,
ol {
    padding-left: 0;
    margin-bottom: 15px;
}
li {
    padding-left: 30px;
    position: relative;
    margin-bottom: 10px;
}
.content li::before {
    content: "";
    display: block;
    width: 10px;
    height: 10px;
    background-color: $pink-color;
    transform: rotate(45deg);
    position: absolute;
    left: 7px;
    top: 5px;
}
a {
    color: $pink-color;
    text-decoration: none;
}
.btn {
    padding: 10px 15px;
    margin-bottom: 15px;
    border-radius: 6px;
    transition: $transition;
    cursor: pointer;
    border-radius: 6px;
}
.btn-black {
    background-color: $black-color;
    border: 1px solid transparent;
    color: #fff;

    &:hover {
        background-color: #333333;
    }
}
.btn-pink {
    background-color: $pink-color;
    border: 1px solid transparent;
    color: #fff;

    &:hover {
        background-color: #af739e;
    }
} 
.btn-white {
    background-color: #fff;
    border: 1px solid #d18abda2;
    color: $pink-color;

    &:hover {
        border-color: $pink-color;
    }
}
.btn-light-white {
    background-color: #fff;
    color: $black-color;

    &:hover {
        background-color: #ffffffa9;
    }
}
.btn-transparent-light {
    background-color: transparent;
    border: 1px solid rgba(255, 255, 255, 0.5);
    color: #fff;

    &:hover {
        border-color: #fff;
    }
}
.load-more {
    display: flex;
    justify-content: center;
    align-items: center;
}
.load-more-btn {
    padding: 15px 45px;
    font-size: 20px;
    font-weight: 400;
    transition: $transition;
}
.shiny-animaton {
    -webkit-animation-name: shiny-animaton;
    animation-name: shiny-animaton;
}
.show-more {
    margin-top: 8px;
    display: none;
    font-size: 18px;
    color: $pink-color;
    border-bottom: 1px solid $pink-color;
}
.hide-content-mobile {
    overflow-y: hidden;
    position: relative;

    &::after {
        content: "";
        display: none;
        width: 100%;
        height: 93px;
        position: absolute;
        left: 0;
        bottom: 1px;
        background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #f8f8f8 100%);
    }
    &.open {
        height: 100%;

        &::after {
            display: none;
        }
    }
}
.btn-prev {
    padding: 16px 24px;
    background-image: url("../img/icons/arrow-prev.png");
    background-repeat: no-repeat;
    background-position: center;
    width: 63px;
    height: 40px;
    border-radius: 12px;
    background-color: #fff;
    cursor: pointer;

    &:hover {
        background-color: $purple-color;
    }
}
.btn-next {
    padding: 16px 24px;
    background-image: url("../img/icons/arrow-next.png");
    background-repeat: no-repeat;
    background-position: center;
    width: 63px;
    height: 40px;
    border-radius: 12px;
    background-color: #fff;
    cursor: pointer;

    &:hover {
        background-color: $purple-color;
    }
}
@media(max-width:1199px){
    .container {
        margin: auto 12px;
    }
}
@media(max-width:1160px){
    .container {
        width: auto;
    }
}
@media(max-width:480px){
    .show-more {
        display: inline-block;
    }
    .hide-content-mobile::after {
        display: block;
    }
}
@keyframes scale-animation {
    from {
        transform: scale(1);
    }
    to {
        transform: scale(1.3);
    }
}
@keyframes shiny-animaton {
    0% {
        opacity: 1;
        -webkit-transform-origin: 50% 50%;
        transform-origin: 50% 50%;
        -webkit-transform: scale(1);
        transform: scale(1);
        -webkit-filter:blur(0);
        filter:blur(0);
    }
    100% {
        opacity: 0;
        -webkit-transform-origin: 50% 50%;
        transform-origin: 50% 50%;
        -webkit-transform: scale(2);
        transform: scale(2);
        -webkit-filter:blur(20px);
        filter:blur(20px);
    }
}