.articles {
    margin-top: 28px;
    padding-bottom: 75px;

    &__title {
        margin-top: 0;
        margin-bottom: 50px;
        @include page-title-one;
    }

    &__list {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 20px;

        &--news {
            .article:first-child {
                grid-column: 1/3;
            }
        }
    }

    .load-more {
        grid-column: 1/5;
        color: #fff;
        font-size: 24px;

        &-btn {
            margin: 20px 0;
        }
    }
    &__pagination {
        margin-top: 24px;
        display: flex;
        justify-content: center;
    }
    &__prev-page {
        border: 1px solid #1414149c;
        margin-bottom: 0;
        margin-right: 10px;
        
        &:hover {
            background-color: #fff;
            border-color: $black-color;
        }
    }
    &__next-page {
        border: 1px solid #1414149c;
        margin-bottom: 0;
        margin-left: 10px;
        &:hover {
            background-color: #fff;
            border-color: $black-color;
        }
    }
    &__link-number {
        color: $black-color;
        transition: .4s;
        @include font-alatsi;

        &:hover {
            color: $pink-color;
        }

        &.current {
            padding: 6px;
            border-radius: 6px;
            background-color: $black-color;
            color: #fff;
        }
    }
    &__nav {
        display: flex;
        gap: 10px;
        align-items: center;
    }
}
.article {
    border-radius: 6px;
    transition: $transition;
    background-color: $grey-color;
    cursor: pointer;

    &__img {
        width: 100%;

        img {
            object-fit: cover;
            width: 100%;
            max-height: 160px;
            border-top-left-radius: 6px;
            border-top-right-radius:6px;
        }
    }
    &__wrapper {
        padding: 16px;
    }
    &__title {
        color: $black-color;
        font-size: 16px;
        line-height: 120%;
        font-weight: 700;
        text-transform: uppercase;
        margin-bottom: 10px;
    }
    &__description {
        color: #959595;
        font-weight: 400;
        font-size: 14px;
        display: -webkit-box;
        -webkit-line-clamp:2;
        margin-bottom: 20px;
    }
    &__date {
        color: $black-color;
        font-size: 12px;
        font-weight: 400;
        text-align: end;
    }
    &:hover {   
        box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.25);
    }

}
@media(max-width: 1199px){
    .articles {
        margin-top: 28px;
    }
}
@media(max-width: 1024px){
    .articles__title {
        font-size: 46px;
        margin-bottom: 35px;
    }
    .articles__list {
        grid-template-columns: repeat(3, 1fr);
    }
    .articles .load-more {
        grid-column: 1/4;
    }
    .articles__list--news .article:nth-last-child(-n+2){
        grid-column: 2/4;
    }
}
@media(max-width: 967px){
    .articles__title {
        font-size: 42px;
        margin-bottom: 25px;
    }
}
@media(max-width: 860px){
    .articles__title {
        font-size: 38px;
        margin-bottom: 20px;
    }
}
@media(max-width:768px){
    .articles {
        margin-top: 15px;
        padding-bottom: 45px;
    }
}
@media(max-width:700px){
    .articles__list {
        grid-template-columns: repeat(2, 1fr);
    }
    .articles .load-more {
        grid-column: 1/3;
    }
    .articles__list--news .article:first-child {
        grid-column: unset;
    }
    .articles__list--news .article:nth-last-child(-n+2){
        grid-column: 1/3;
    }
}
@media(max-width: 630px){
    .articles__title {
        font-size: 34px;
    }
}
@media(max-width: 585px){
    .articles__title {
        font-size: 30px;
    }
    .articles .load-more-btn {
        font-size: 20px;
    }
}
@media(max-width:500px){
    .articles__list {
        grid-template-columns: 1fr;
    }
    .articles .load-more {
        grid-column: unset;

    }
    .articles .load-more-btn {
        width: 100%;
        text-align: center;
    }
    .articles .load-more-btn {
        font-size: 18px;
        padding: 10px 30px;
    }
    .articles__list--news .article:nth-last-child(-n+2){
        grid-column: unset;
    }
}
@media(max-width: 480px){
    .articles__title {
        font-size: 28px;
    }
}
@media(max-width: 360px){
    .articles__title {
        font-size: 26px;
    }
}
@media(max-width: 330px){
    .articles__title {
        font-size: 24px;
    }
}