.modal-del-find {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: none;
    justify-content: center;
    align-items: center;
    background-color: #000000a1;
    z-index: 20;

    &__body {
        background-color: #fff;
        padding: 45px 30px;
        width: 900px;
        position: relative;
        border-radius: 12px;
    }
    &__title {
        font-size: 38px;
        text-transform: uppercase;
        font-weight: 700;
        margin-bottom: 30px;
    }
    &__desc {
        font-size: 20px;
        margin-bottom: 50px;
    }
    &__close {
        width: 40px;
        height: 40px;
        background-image: url("../img/icons/icon-close.png");
        background-repeat: no-repeat;
        background-size: contain;
        right: 10px;
        top: 10px;
        background-position: center;
        position: absolute;
        cursor: pointer;
    }
    &__btns {
        display: flex;
        column-gap: 30px;

        .btn {
            font-size: 18px;
            margin-bottom: 0;
            min-width: 220px;
        }
    }
}
@media(max-width:900px){
    .modal-del-find__body {
        margin: 0 12px;
    }
}
@media(max-width:594px){
    .modal-del-find__title {
        font-size: 32px;
    }
    .modal-del-find__desc {
        font-size: 18px;
    }
    .modal-del-find__close {
        width: 30px;
        height: 30px;
    }
    .modal-del-find__btns .btn {
        min-width: unset;
        width: 100%;
    }
    .modal-del-find__btns {
        flex-direction: column;
        gap: 12px;
    }
}
@media(max-width:390px){
    .modal-del-find__title {
        font-size: 28px;
        margin-bottom: 16px;
    }
    .modal-del-find__desc {
        font-size: 16px;
    }
    .modal-del-find__body {
        padding: 45px 15px 30px;
    }
}
@media(max-width:370px){
    .modal-del-find__title {
        font-size: 24px;
    }
    .modal-del-find__desc {
        font-size: 14px;
    }
}