.error-404 {
    display: flex;
    padding-top: 40px;
    padding-bottom: 120px;
    align-items: center;
    justify-content: space-evenly;
    column-gap: 20px;

    &__text {
        font-size: 28px;
        text-transform: uppercase;
        text-align: center;
        font-weight: 300;
        margin-top: 45px;
        margin-bottom: 50px;
    }
    &__number {
        display: flex;
        justify-content: center;
    }
    &__btn {
        text-align: center;
    }
    &__link {
        color: #fff;
        text-transform: uppercase;
        font-size: 18px;
        font-weight: 300;
        padding: 15px 45px;
        background-color: $black-color;
    }
}
@media(max-width:1199px){
    .error-404 {
        margin-top: 28px;
    }
}
@media(max-width:630px){
    .error-404__right {
        display: none;
    }
}
@media(max-width:567px){
    .error-404__text {
        font-size: 24px;
        margin-bottom: 30px;
    }
}
@media(max-width:480px){
    .error-404__link {
        display: inline-block;
        width: 100%;
        text-align: center;
    }
    .error-404 {
        padding-bottom: 85px;
    }
}
@media(max-width:420px){
    .error-404__text {
        margin-top: 30px;
        margin-bottom: 30px;
        font-size: 20px;
    }
}