.shinydex {
    padding-bottom: 65px;

    &__title {
        text-transform: uppercase;
        @include page-title-one;
    }

    .filter-tabs {
        margin-bottom: 20px;
    }

    &__list {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 25px;

        .load-more {
            grid-column: 1/5;
            justify-content: center;
        }
    }
    &__pokemon {
        
        transition: $transition;
        background-color: $grey-color;
        cursor: pointer;
        min-height: 260px;
        padding: 14px;
        display: flex;
        flex-direction: column;
        transition: $transition;
        position: relative;
        border-radius: 6px;
        border: 1px solid transparent;

        &:hover {
            box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.25);
        }

        &.completed {
            background-color: #fff;
            border-color: rgba(123, 120, 168, 0.568627451);

            .shinydex__btn {
                background-image: url("../img/icons/pink-galochka.png");
            }
        }
    }
    &__pok-number {
        font-size: 20px;
        color: $black-color;
        font-weight: 700;
    }
    &__pok-img {
        display: flex;
        justify-content: center;
        margin-bottom: 10px;

        img {
            max-width: 162px;
            object-fit: cover;
        }
    }
    &__pok-name {
        text-align: center;
    }
    &__pok-link {
        font-size: 20px;
        font-weight: 700;
        text-transform: uppercase;
        color: $black-color;
        border-bottom: 1px solid $black-color;
        cursor: pointer;
        transition: $transition;

        &::before {
            content: "";
            width:100%;
            height:100%;
            position: absolute;
            top: 0;
            left: 0;
        }
    }
    &__pok-wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__btn {
        width: 29px;
        height: 29px;
        background-image: url("../img/icons/black-sparkles.png");
        background-repeat: no-repeat;
        cursor: pointer;
        transition: $transition;
        background-color: transparent;
        border: none;
        box-shadow: none;
        margin-bottom: 0;
        position: relative;
        z-index: 10;

        &.checked {
            background-image: url("../img/icons/pink-galochka.png");
        }
        &:hover {
            background-image: url("../img/icons/pink-sparkles.png");
        }
    }

    .load-more {
        margin-top: 50px;
    }
}
@media(max-width:1199px){
    .shinydex {
        margin-top: 28px;
    }
}
@media(max-width: 1024px){
    .shinydex__title {
        font-size: 46px;
        margin-top: 0;
        margin-bottom: 35px;
    }
    .shinydex__list {
        grid-template-columns: repeat(3,1fr);
    }
    .shinydex__list .load-more {
        grid-column: 1/4;
    }
}
@media(max-width: 967px){
    .shinydex__title {
        font-size: 42px;
        margin-bottom: 25px;
    }
    .shinydex .load-more {
        margin-top: 30px;
    }
}
@media(max-width: 860px){
    .shinydex__title {
        font-size: 38px;
        margin-bottom: 20px;
    }
    .shinydex__pok-img img {
        max-width: 140px;
    }
}
@media(max-width:720px){
    .shinydex__list {
        grid-template-columns: repeat(2,1fr);
    }
    .shinydex__list .load-more {
        grid-column: 1/3;
    }
}
@media(max-width: 630px){
    .shinydex__title {
        font-size: 34px;
    }
}
@media(max-width: 585px){
    .shinydex__title {
        font-size: 30px;
    }
}
@media(max-width: 525px){
    .shinydex .load-more-btn{
        font-size: 20px;
    }
}
@media(max-width:515px){
    .shinydex__pok-img img {
        max-width: 120px;
    }
    .shinydex__pok-wrapper {
        margin-top: auto;
    }
}
@media(max-width: 500px){
    .shinydex .load-more-btn {
        width: 100%;
        text-align: center;
        font-size: 18px;
    }
    .shinydex .load-more {
        margin-top: 0px;
    }
}
@media(max-width: 480px){
    .shinydex__title {
        font-size: 28px;
    }
    .shinydex__list {
        grid-template-columns: 1fr;
    }
    .shinydex__list .load-more {
        grid-column: unset;
    }
    .shinydex__pok-img img {
        max-width: 162px;
    }
    .shinydex {
        padding-bottom: 30px;
    }
}
@media(max-width: 360px){
    .shinydex__title {
        font-size: 26px;
    }
}
@media(max-width: 330px){
    .shinydex__title {
        font-size: 24px;
    }
}