.sort-select {
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
    align-items: center;
    margin-bottom: 40px;

    &__title {
        font-size: 13px;
        font-weight: 700;
        color: $black-color;
    }
    &__body {
        position: absolute;
        min-width: 170px;
        width: 100%;
        top: 29px;
        cursor: pointer;
        display: none;
        z-index: 15;
        box-shadow: $box-shadow;
    }
    &__head {
        min-width: 170px;
        background-color: $grey-color;
        border-radius: 6px;
        padding: 6px;
        text-align: center;
        color: $black-color;
        font-size: 13px;
        font-weight: 400;
        position: relative;
        cursor: pointer;
        transition: $transition;

        &::after {
            content: "";
            display: block;
            width: 15px;
            height: 10px;
            background-image: url("../img/icons/arrow-down.png");
            background-repeat: no-repeat;
            background-size: contain;
            background-position: center;
            position: absolute;
            top: 10px;
            right: 10px;
            transition: $transition;
            cursor: pointer;
        }

        &:hover {
            box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.25);
        }
    }

    &__container {
        position: relative;

        &.active {
            .sort-select__head::after {
                transform: rotate(180deg);
            }
        }
    }
    &__reset {
        display: none;
        font-size: 13px;
        padding: 6px;
        min-width: 170px;
        text-align: center;
        border-radius: 6px;
        border: 1px solid #1414149d;
        transition: .4s;
        cursor: pointer;

        &:hover {
            border-color: $black-color;
        }
    }
}
.sort-option {
    padding: 6px;
    font-size: 13px;
    text-align: center;
    background-color: #fff;
    border-bottom: 1px solid #f0f0f0;
    cursor: pointer;
    transition: $transition;

    &:hover {
        background-color: $purple-color;
        border-color: $purple-color;
        color: #fff;
    }
}
.counter__settings-body .sort-select {
    margin-bottom: 10px;
}
.counter__settings-body .sort-select__head {
    background-color: #fff;
}
@media(max-width:480px){
    .sort-select {
        margin-bottom: 25px;
    }
}
@media(max-width:400px){
    .sort-select {
        flex-direction: column;
        align-items: unset;
        row-gap: 10px;
    }
}