@import 'mixins.scss';
@import 'vars.scss';

.header {
    position: fixed;
    top: 0;
    z-index: 19;
    width: 100%;

    &__mobile-btns {
        display: none;
        margin-top: 64px;
        gap: 24px;
        padding: 0 30px;
    }
    
    &__top {
        padding: 18px 0;
        background-color: $grey-color;

        .container {
            display: flex;
            justify-content: space-between;
        }

        &-container {
            display: flex;
            align-items: center;
            column-gap: 25px;
        }
    }
    &__logo {
        height: 80px;

        img {
            object-fit: contain;
        }
    }
    &__collection {
        width: 35px;
        height: 35px;
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
        cursor: pointer;
        transition: $transition;
        background-image: url("../img/icons/LOGO-black.png");

        &:hover {
            background-image: url("../img/icons/LOGO-pink.png");
        }
    }
    &__login {
        width: 35px;
        height: 35px;
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
        background-image:url("../img/icons/personal-kabinet.png");
        transition: $transition;

        &:hover {
            background-image:url("../img/icons/personal-kabinet-pink.png");
        }
    }
    &__search{
        padding: 18px 0;
        display: none;
        background-color: $dark-grey;

        &-form {
            display: flex;
            gap: 10px;
        }
        &-btn {
            min-width: 100px;
            font-size: 18px;
            line-height: 120%;
            @include font-raleway;
            margin-bottom: 0;
        }
        &-mobile {
            padding: 0 30px;
            display: flex;
            height: 35px;
            .header__search-input {
                right: 0;
                display: none;
                transition: none;
                width: 100%;
                
                input[type=text] {
                    height: 35px;
                    border-bottom: 1px solid #ffffff;
                    color: #fff;

                    &::placeholder {
                        color: #a8a8a88a;
                    }
                    &:hover {
                        border-color: #ffffff;
                    }
                }
            }
            input[type="submit"]{
                display: none;
            }
            .header__search-icon {
                position: absolute;
                right: 30px;
                width: 30px;
                height: 30px;
                background-image: url("../img/icons/search-icon-white.png");
                transition: none;

                &:hover {
                    background-image: url("../img/icons/search-icon-white.png");
                }
            }
        }
        &-input {
            transition: $transition;
            position: relative;
            width: 100%;

            & input[type="text"]{
                width: 100%;
                height: 100%;
                font-size: 18px;
                line-height: 120%;
                border: none;
                padding: 5px;
                padding-left: 10px;
                outline: none;
                background-color: transparent;
                border-bottom: 1px solid $black-color;
                transition: $transition;
                @include font-raleway;

                &::placeholder {
                    color: #535353;
                    font-size: 18px;
                    @include font-raleway;
                }
                &:focus,
                &:hover {
                    border-color: $pink-color;
                }
            }
        }
        &-icon {
            width: 35px;
            height: 35px;
            background-repeat: no-repeat;
            background-size: contain;
            position: relative;
            z-index: 2;
            background-image: url("../img/icons/search-icon.png");
            background-position: center;
            transition: $transition;
            background-color: transparent;
            outline: none;
            border: none;
            cursor: pointer;

            &:hover{
                background-image: url("../img/icons/search-icon-hover.png");
            }
            &.open {
                background-image: url("../img/icons/icon-close.png");
            }
        }
    }
    &__bottom {
        width: 100%;
        background-color: $black-color;
        position: relative;
    }
    &__burger-btn {
        display: none;
        position: relative;
        margin-top: 10px;
        height: 30px;

        &.active {
            .header__burger-icon {
                background: none;
            }

        }
    }
    &__burger-icon {
        display: block;
        position: relative;
        background-color: $black-color;
        height: 4px;
        width: 100%;
        border-radius: 20px;
        -webkit-transition: background 0s .3s, width .3s .3s;
        -o-transition: background 0s .3s, width .3s .3s;
        transition: background 0s .3s, width .3s .3s;
        margin: 35px 0;

        &::before,
        &::after {
            content: "";
            position: absolute;
            background-color: $black-color;
            right: 0;
            display: block;
            height: 100%;
            width: 100%;
            border-radius: 50px;
            -webkit-transition: bottom .3s .3s, width .3s .6s, -webkit-transform .3s 0s;
            transition: bottom .3s .3s, width .3s .6s, -webkit-transform .3s 0s;
            -o-transition: transform .3s 0s, bottom .3s .3s, width .3s .6s;
            transition: transform .3s 0s, bottom .3s .3s, width .3s .6s;
            transition: transform .3s 0s, bottom .3s .3s, width .3s .6s, -webkit-transform .3s 0s
        }   
        &::after {
            bottom: 10px
        }
        &:before {
            bottom: -10px;
            width: 20px;
        }

    }
    &__menu {
        margin: 0;
        display: flex;
        max-width: 750px;
        column-gap: 20px;

        &-link {
            margin: 0;
            display: flex;
            column-gap: 10px;
            padding-top: 25px;
            padding-bottom: 25px;
            padding-left: 0;
            justify-content: center;
            align-items: center;
            cursor: pointer;
            transition: $transition;

            &-parent {

                svg {
                    transition: $transition;
                }

                &:hover {
                    svg {
                        transform: rotate(180deg)
                    }
                    ul {
                        padding: 10px 0;
                        max-height: 325px;
                        opacity: 1;
                    }
                }
            }

            a {
                @include font-raleway;
                color: $black-color;
                position: relative;
                font-size: 18px;
                line-height: 110%;
                text-align: center;
                font-weight: 400;

                &::before {
                    content: "";
                    position: absolute;
                    left: 0;
                    bottom: -6px;
                    width: 100%;
                    height: 2px;
                    background-color: $black-color;
                    transform: scaleX(0);
                    transition: $transition;
                }
                &:hover::before {
                    transform: scaleX(1);
                }
            }

            &:first-child {
                border-left: 0;
            }
        }
    }
    &__submenu {
        position: absolute;
        padding: 0;
        width: 100%;
        background-color: $black-color;
        margin: 0;
        top: 70px;
        z-index: 3;
        max-height: 0;
        opacity: 0;
        transition: $transition max-height, $transition opacity, $transition padding;

        &-link {
            padding: 6px 10px;
            margin: 0;

            a {
                color: #fff;
                transition: .4s;

                &:hover {
                    color: $pink-color;
                }
            }
        }
    }
    &__mobile {
        display: none;
        min-width: 40px;
        margin-left: 10px;
        cursor: pointer;
    }
    &__mobile-wrapper {
        display: none;
        padding-top: 30px;
        width: 0;
        position: absolute;
        top: 98px;
        height: 100vh;
        background-color: $black-color;
        z-index: 15;
        right: -100%;
        transition: $transition;
        overflow-x: hidden;
        opacity: 0;

        &.active {
            right: 0;
            opacity: 1;
            width: 100%;
        }
    }
    &__mobile-link {
        margin-bottom: 15px;
        a{
            font-size: 20px;
            color: #fff;
            border-bottom: 1px solid #fff;
        }
        &-parent {
            display: flex;
            justify-content: space-between;
            position: unset;
        }
    }
    &__mobile-submenu {
        visibility:hidden;
        position: absolute;
        width: 100%;
        top: 0;
        height: 100%;
        left: calc(100% + 20px);
        overflow-y: auto;
        transition: visibility 0.4s ease;
        padding: 0 30px;
        &.active {
            visibility:visible;
        }

        &-back {
            color: #fff;
            font-weight: 700;
            font-size: 16px;
            margin-bottom: 32px;
            display: flex;
            gap: 8px;
            align-items: center;

            &::before {
                content: "";
                width: 15px;
                height: 24px;
                display: block;
                background-image: url("../svg/back.svg");
                background-repeat: no-repeat;
                background-size: contain;
                background-position: center;
            }
        }
    }
    &.fixed {
        box-shadow: $box-shadow;
    }
    &__mobile-menu {
        position: relative;
        padding: 0 30px;
        transition: transform .4s;

        &.hide {
            transform: translateX(calc(-100% - 25px));
        }

        li {
            padding-left: 0;
        }
    }
}
.header__burger-btn.active .header__burger-icon:after,
.header__burger-btn.active .header__burger-icon:before {
  bottom: 0;
  -webkit-transition: bottom .3s .3s, width .3s 0s, -webkit-transform .3s .6s;
  transition: bottom .3s .3s, width .3s 0s, -webkit-transform .3s .6s;
  -o-transition: transform .3s .6s, bottom .3s .3s, width .3s 0s;
  transition: transform .3s .6s, bottom .3s .3s, width .3s 0s;
  transition: transform .3s .6s, bottom .3s .3s, width .3s 0s, -webkit-transform .3s .6s
}

.header__burger-btn.active .header__burger-icon:after {
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg)
}

.header__burger-btn.active .header__burger-icon:before {
  width: 100%;
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg)
}
@media(max-width:1160px){
    .header__top {
        padding-left: 15px;
        padding-right: 15px;
    }
}
@media(max-width:1120px){
    .header__top {
        padding-left: 15px;
        padding-right: 15px;
    }
}
@media(max-width:980px){
    .header__menu,
    .header__top-container .header__search-icon {
        display: none;
    }
    .header__burger-btn {
        display: block;
    }
    .header__mobile {
        display: block;
    }
    .header__login {
        width: 35px;
        height: 35px;
    }
    .header__mobile {
        margin-left: 20px;
    }
    .header__top-container {
        margin-left: auto;
    }
    .header__mobile-wrapper {
        display: block;
    }
}
@media(max-width:768px){
    .header__logo {
        height: 50px;
    }
    .header__top {
        padding-top: 15px;
    }
    .header__top-container {
        column-gap: 10px;
    }
    .header__login {
        display: none;
    }
    .header__mobile-btns {
        display: flex;
    }
    .header__search-input input[type=text] {
        height: 25px;

        &::placeholder {
            font-size: 14px;
        }
    }
    .header__search-input {
        width: 250px;
    }
    .header__top .container {
        align-items: end;
    }
}
@media(max-width:530px){
    .header__search-mobile .header__search-input {
        width: 75%;
    }
    .header__top-container {
        display: none;
    }
}
@media(max-width:480px){
    .header__mobile-btns {
        flex-direction: column;
        
        gap: 12px;
    }
    .header__mobile-btns .btn{
        margin-bottom: 0;
        text-align: center;
    }
}
@media(max-width:360px){
    .header__search-mobile .header__search-icon {
        right: 15px;
    }
}