.other-posts {
    background-color: #fff;
    padding-top: 65px;
    padding-bottom: 65px;

    &__title {
        margin: 0;
        margin-bottom: 30px;
        font-size: 38px;
        font-weight: 600;
        color: $black-color;
        text-transform: uppercase;
    }

    &__list {
        display: grid;
        grid-template-columns: repeat(4,1fr);
        column-gap: 20px;
        row-gap: 20px;
    }
}
@media(max-width: 1199px){
    .other-posts .container {
        margin-top: 28px;
        width: auto;
    }
}
@media(max-width: 1024px){
    .other-posts__title {
        font-size: 36px;
    }
    .article-detail__content img {
        width: 100%;
    }
}
@media(max-width:967px){
    .other-posts__list {
        grid-template-columns: repeat(3,1fr);
    }
    .other-posts__title {
        font-size: 34px;
    }
}
@media(max-width: 860px){
    .other-posts__title {
        font-size: 32px;
    }
}
@media(max-width:768px){
    .other-posts {
        padding: 30px 0;
    }
    .other-posts .container {
        margin-top: 0;
    }
}
@media(max-width:725px){
    .other-posts__list {
        grid-template-columns: repeat(2,1fr);
    }
}
@media(max-width: 630px){
    .other-posts__title {
        font-size: 28px;
        margin-bottom: 20px;
    }
}
@media(max-width:480px){
    .other-posts__list {
        grid-template-columns: 1fr;
    }
    .other-posts__title {
        font-size: 26px;
    }
}
@media(max-width: 360px){
    .other-posts__title {
        font-size: 24px;
    }
}
@media(max-width: 330px){
    .other-posts__title {
        font-size: 22px;
    }
}