.pokemon {
    &__name {
        @include page-title-one;
    }
    &__wrapper {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-areas: "img rang"
                             "img type"
                             "img weak"
                             "img info";
        column-gap: 45px;
        min-height: 560px;
        margin-bottom: 55px;
    }
    &__container {
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: 45px;
    }
    &__image {
        background-color: $grey-color;
        display: flex;
        justify-content: center;
        align-items: flex-start;
        position: relative;
        grid-area: img;
        border-radius: 12px;
    }
    &__sparkle {
        width: 50px;
        height: 50px;
        position: absolute;
        background-image: url("../img/icons/black-sparkles-50.png");
        background-repeat: no-repeat;
        background-size: contain;
        bottom: 35px;
        right: 35px;
        transition: .4s;
        cursor: pointer;

        &:hover {
            background-image: url("../img/icons/pink-sparkles.png");
        }
    }
    &__wrapper-rang {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 25px;
        grid-area: rang;
    }
    &__rang {
        font-size: 36px;
        font-weight: 400;
        @include font-alatsi;
        text-transform: uppercase;

    }
    &__info-type {
        grid-area: type;
    }
    &__info-weakness {
        grid-area: weak;
    }
    &__about {
        grid-area: info;
    }
    &__counter-link {
        display: inline-block;
        transition: $transition;
        color: $black-color;
        background-color: transparent;
        border: 1px solid rgb(20 20 20 / 24%);
        border-radius: 6px;
        margin-bottom: 0;

        &:hover {
            border-color: $black-color;
        }
    }
    &__type-name,
    &__weakness-name {
        font-size: 24px;
        text-transform: uppercase;
        font-weight: 700;
        margin-bottom: 15px;
    }
    &__types {
        display: flex;
        column-gap: 20px;
        margin-bottom: 30px;
    }
    &__type {
        max-width: 215px;
        padding: 12px 65px;
        color: #fff;
        border-radius: 6px;
        font-weight: 700;
        font-size: 18px;
        text-transform: uppercase;
    }
    &__weaknesses {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 20px;
        margin-bottom: 20px;
    }
    &__weakness {
        color: #fff;
        font-size: 18px;
        text-transform: uppercase;
        font-weight: 700;
        padding: 10px;
        border-radius: 6px;
        text-align: center;

        sup {
            font-size: 12px;
        }
    }
    &__about {
        background-color: $black-color;
        padding: 15px;
        color: #fff;
        display: flex;
        justify-content: space-between;
        height: 100%;
        border-radius: 6px;
    }
    &__info {
        display: flex;
        flex-direction: column;
    }
    &__height-name,
    &__weight-name,
    &__gender-name,
    &__ability-name,
    &__hidden-name {
        font-size: 20px;
        font-weight: 700;
        text-transform: uppercase;
        letter-spacing: 0.05em;
        @include font-raleway;
        margin-bottom: 5px;
    }
    &__height,
    &__weight {
        font-size: 20px;
        font-weight: 400;
        @include font-raleway;
        margin-bottom: 30px;
    }
    &__weight {
        margin-bottom: 0;
    }
    &__gender-name {
        margin-bottom: 20px;
    }
    &__percent-male,
    &__percent-female {
        @include font-raleway;
        font-size: 20px;
        font-weight: 300;
        padding-left: 40px;
        margin-bottom: 20px;
        position: relative;
    }

    &__percent-male::before {
        content: "";
        display: block;
        width: 24px;
        height: 36px;
        background-image: url("../img/icons/gender-male.png");
        background-repeat: no-repeat;
        background-size: contain;
        position: absolute;
        left: 0;
        bottom: -5px;
        transform: rotate(35deg);
    }
    &__percent-female::before {
        content: "";
        display: block;
        width: 24px;
        height: 36px;
        background-image: url("../img/icons/gender-female.png");
        background-repeat: no-repeat;
        background-size: contain;
        position: absolute;
        left: 0;
    }
    &__ability-name,
    &__hidden-name {
        margin-bottom: 5px;
    }
    &__ability,
    &__hidden {
        font-size: 20px;
        font-weight: 300;
        border-bottom: 1px solid #fff;
        @include font-raleway;
        color: #fff;
    }
    &__ability-container {
        margin-bottom: 25px;
    }

    &__descr-title {
        @include page-title-two;
    }
    &__descr-content {
        color: $black-color;
        font-size: 18px;
        margin-bottom: 50px;
    }
    &__location-title {
        @include page-title-two;
        margin-bottom: 45px;
    }
    &__other-title {
        @include page-title-two;
        margin-bottom: 45px;
    }
    &__location-list {
        display: flex;
        flex-direction: column;
    }
    &__location {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        padding: 18px 45px;
        border-top: 1px solid #f0f0f0;
        justify-content: space-between;

        &:last-child {
            border-bottom: 1px solid #f0f0f0;
        }
    }
    &__game {
        font-weight: 700;
        font-size: 20px;
    }
    &__loc-info {
        text-align: center;
        font-size: 20px;
        font-weight: 400;
        @include font-raleway;
    }
    &__forms-title {
        @include page-title-three;
    }
    &__forms-img {
        display: flex;
        justify-content: center;
    }
    &__forms-img img{
        max-height: 235px;
    }
    &__breed-title {
        @include page-title-three;
    }
    &__egg-group {
        color: #4f4f4f;
        font-size: 16px;
        font-weight: 500;
        line-height: 117%;
        text-transform: uppercase;
        margin-bottom: 15px;

        span {
            display: inline-block;
            margin-left: 15px;
            color: $black-color;
        }
    }
    &__cycle {
        color: #4f4f4f;
        font-size: 16px;
        font-weight: 500;
        line-height: 117%;
        text-transform: uppercase;

        span {
            display: inline-block;
            margin-left: 15px;
            color: $black-color;
        }
    }
    &__breeding {
        margin-bottom: 50px;
    }
    &__evolutions {
        margin-bottom: 50px;
        &-title {
            @include page-title-two;
        }
        &-list {
            display: flex;
            justify-content: center;
        }
    }
    &__img,
    &__item {
        display: flex;
        justify-content: center;
        align-items: bottom;
    }
    &__img img {
        max-height: 248px;
    }
    &__next-icon {
        width: 150px;
        height: 50px;
        background-image: url("../img/icons/black-arrow.png");
        background-repeat: no-repeat;
        background-size: contain;

        &.reverse {
            transform: scaleY(-1);
        }
    }
    &__next-row {
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
    }
    &__level {
        position: absolute;
        bottom: 50px;
    }
    &__pagination {
        display: flex;
        justify-content: space-between;
    }
    &__prev {
        display: flex;
        column-gap: 20px;
        align-items: center;
        &::before {
            content: "";
            display: block;
            width: 36px;
            height: 36px;
            background-image: url("../img/icons/prev-arrow.png");
            background-repeat: no-repeat;
            background-size: contain;
        }
        
    }
    &__next {
        display: flex;
        flex-direction: row-reverse;
        column-gap: 20px;
        align-items: center;
        &::before {
            content: "";
            display: block;
            width: 36px;
            height: 36px;
            background-image: url("../img/icons/next-arrow.png");
            background-repeat: no-repeat;
            background-size: contain;
        }
        
    }
    &__pagination {
        margin-top: 70px;
        margin-bottom: 50px;
    }
    &__page-link {
        color: $black-color;
        border-bottom: 3px solid $black-color;
        font-size: 28px;
        line-height: 115%;
        font-weight: 700;
        transition: $transition;

        &:hover {
            color: $pink-color;
            border-color: $pink-color;
        }
    }
    &__evolutions {
        margin-top: 50px;
    }
}
.ability-info {
    &__title {
        @include page-title-three;
    }
    &__name,
    &__hidden {
        font-size: 20px;
        font-weight: 700;
        margin-bottom: 10px;
    }
    &__text,
    &__hidden-descr {
        color: $black-color;
        font-size: 16px;
        margin-bottom: 25px;
    }
}
@media(max-width:1199px){
    .pokemon {
        margin-top: 28px;
    }
}
@media(max-width:1050px){
    .pokemon__weaknesses {
        grid-template-columns: repeat(2, 1fr);
    }
    .pokemon__type {
        min-width: unset;
        width: 48%;
    }
}
@media(max-width:1024px){
    .pokemon__name {
        font-size: 46px;
        margin-top: 0;
        margin-bottom: 35px;
    }
    .pokemon__descr-title,
    .pokemon__location-title,
    .pokemon__other-title,
    .pokemon__evolutions-title {
        font-size: 36px;
    }
}
@media(max-width: 967px){
    .pokemon__name {
        font-size: 42px;
        margin-bottom: 25px;
    }
    .pokemon__wrapper {
        column-gap: 35px;
    }
    .pokemon__wrapper {
        grid-template-areas: "img rang"
                             "img type"
                             "img weak"
                             "info info";
    }
    .pokemon__about {
        margin-top: 30px;
    }
    .pokemon__img {
        max-height: 320px;
    }
    .pokemon__type-name, .pokemon__weakness-name {
        font-size: 20px;
    }

    .pokemon__type, .pokemon__weakness {
        font-size: 18px;
    }
    .pokemon__rang {
        font-size: 32px;
    }
    .pokemon__descr-title,
    .pokemon__location-title,
    .pokemon__other-title,
    .pokemon__evolutions-title {
        font-size: 34px;
        margin-bottom: 40px;
    }
    .pokemon__next-icon {
        width: 100px;
    }
    .pokemon__img img {
        max-height: 200px;
        object-fit: contain;
    }
}
@media(max-width:920px){
    .pokemon__container {
        grid-template-columns: 1fr;
    }
    .pokemon__forms {
        display: flex;
        justify-content: space-evenly;
        flex-wrap: wrap;
        row-gap: 30px;
    }
    .pokemon__forms-title {
        text-align: center;
    }
}
@media(max-width: 860px){
    .pokemon__name {
        font-size: 38px;
        margin-bottom: 20px;
    }
    .pokemon__img {
        max-width: 270px;
    }
    .pokemon__wrapper {
        column-gap: 25px;
    }
    .pokemon__descr-title,
    .pokemon__location-title,
    .pokemon__other-title,
    .pokemon__evolutions-title {
        font-size: 32px;
        margin-bottom: 30px;
    }
}
@media(max-width:767px){
    .pokemon__types {
        flex-direction: column;
        row-gap: 10px;
        margin-bottom: 0;
    }
    .pokemon__type {
        display: inline-block;
        min-width: 100%;
        text-align: center;
        
    }
    .pokemon__wrapper {
        grid-template-areas: "img rang"
                             "img type"
                             "weak weak"
                             "info info";
    }
    .pokemon__info-weakness {
        margin-top: 30px;
    }
    .pokemon__sparkle {
        right: 15px;
        bottom: 15px;
    }
    .pokemon__image {
        padding-bottom: 50px;
    }
    .pokemon__wrapper-rang {
        align-items: flex-start;
    }
    .pokemon__info-type {
        margin-top: auto;
    }
    .pokemon__forms-title,
    .pokemon__breed-title,
    .ability-info__title {
        font-size: 22px;
    }
    .pokemon__descr-content {
        font-size: 16px;
    }
    .pokemon__game,
    .pokemon__loc-info {
        font-size: 18px;
    }
    .pokemon__forms-img img {
        max-height: 185px;
    }
    .pokemon__level {
        bottom: 30px;
    }
}
@media(max-width:685px){
    .pokemon__evolutions-list {
        flex-direction: column;
    }
    .pokemon__item {
        flex-direction: column;
    }
    .pokemon__img {
        align-self: center;
    }
    .pokemon__next-icon {
        transform: rotate(84deg);
        right: -76px;
        position: relative;
    }
    .pokemon__next-icon.reverse {
        transform: rotate(102deg) scaleY(-1);
        left: -65px;
        position: relative;
    }
    .pokemon__level {
        bottom: 0;
    }
    .pokemon__pagination {
        margin-top: 40px;
    }
    .pokemon__page-link {
        font-size: 24px;
    }
    .pokemon__prev::before,
    .pokemon__next::before {
        width: 30px;
        height: 30px;
    }
    .pokemon__prev,
    .pokemon__next {
        column-gap: 15px;
    }
}
@media(max-width: 630px){
    .pokemon__name {
        font-size: 34px;
    }
    .pokemon__wrapper {
        grid-template-columns: 1fr 1fr;
        grid-template-areas: "rang rang"
                             "img img"
                             "type weak"
                             "info info";
    }
    .pokemon__weaknesses {
        grid-template-columns: 1fr;
    }
    .pokemon__info-type {
        margin-top: 30px;
    }
    .pokemon__descr-title,
    .pokemon__location-title,
    .pokemon__other-title,
    .pokemon__evolutions-title {
        font-size: 28px;
    }
    .pokemon__forms-title,
    .pokemon__breed-title,
    .ability-info__title {
        font-size: 20px;
    }
    .ability-info__name, .ability-info__hidden {
        font-size: 18px;
    }
}
@media(max-width: 585px){
    .pokemon__name {
        font-size: 30px;
    }

}
@media(max-width: 480px){
    .pokemon__name {
        font-size: 28px;
        margin-bottom: 10px;
    }
    .pokemon__height-name, .pokemon__weight-name, .pokemon__gender-name, .pokemon__ability-name, .pokemon__hidden-name {
        font-size: 16px;
    }
    .pokemon__wrapper {
        grid-template-columns: 1fr 1fr;
        grid-template-areas: "rang rang"
                             "img img"
                             "type type"
                             "weak weak"
                             "info info";
    }
    .pokemon__descr-title,
    .pokemon__location-title,
    .pokemon__other-title,
    .pokemon__evolutions-title {
        font-size: 26px;
        margin-bottom: 15px;
    }
    .pokemon__info-type,
    .pokemon__info-weakness,
    .pokemon__about {
        margin-top: 20px;
    }
    .pokemon__weaknesses {
        margin-bottom: 10px;
    }
    .pokemon__type, .pokemon__weakness {
        font-size: 16px;
        padding: 8px;
    }
    .pokemon__weaknesses {
        gap: 10px;
    }
    .pokemon__forms-title,
    .pokemon__breed-title,
    .ability-info__title {
        font-size: 18px;
    }
    .ability-info__name, .ability-info__hidden {
        font-size: 16px;
    }
    .pokemon__descr-content {
        font-size: 14px;
    }
    .ability-info__text, .ability-info__hidden-descr {
        font-size: 14px;
    }
    .pokemon__location {
        padding: 12px 30px;
    }
    .pokemon__loc-info,
    .pokemon__game {
        font-size: 16px;
    }
    .pokemon__forms-img img {
        max-height: 165px;
    }
    .pokemon__page-link {
        font-size: 18px;
    }
    .pokemon__prev::before, .pokemon__next::before {
        width: 26px;
        height: 26px;
    }
    .pokemon__prev, .pokemon__next {
        column-gap: 10px;
    }
    .pokemon__sparkle {
        width: 40px;
        height: 40px;
    }
    .pokemon {
        margin-top: 15px;
    }
    .pokemon__wrapper-rang {
        margin-bottom: 15px;
    }
    .pokemon__wrapper {
        margin-bottom: 45px;
    }
    .pokemon__descr-content {
        margin-bottom: 20px;
    }
    .pokemon__breeding {
        margin-bottom: 35px;
    }
}
@media(max-width:400px){
    .pokemon__about {
        flex-wrap: wrap;
        justify-content: space-around;
        row-gap: 10px;
    }
    .pokemon__info-abilities {
        width: 100%;
        text-align: center;
    }
    .pokemon__ability-container {
        margin-bottom: 15px;
    }
}
@media(max-width: 360px){
    .pokemon__name {
        font-size: 26px;
    }
    .pokemon__descr-title,
    .pokemon__location-title,
    .pokemon__other-title {
        font-size: 24px;
    }
    .pokemon__forms-title,
    .pokemon__breed-title,
    .ability-info__title {
        font-size: 16px;
    }
    .ability-info__name, .ability-info__hidden {
        font-size: 14px;
    }
    .pokemon__egg-group,
    .pokemon__cycle {
        font-size: 14px;
    }
    .pokemon__page-link {
        font-size: 16px;
    }
}
@media(max-width:340px){
    .pokemon__page-link {
        font-size: 14px;
    }
}
@media(max-width: 330px){
    .pokemon__name {
        font-size: 24px;
    }
    .pokemon__descr-title,
    .pokemon__location-title,
    .pokemon__other-title,
    .pokemon__evolutions-title {
        font-size: 22px;
        margin-bottom: 10px;
    }
}