.last-articles {
    padding: 65px 0;
    background-color: $grey-color;

    &__title {
        margin: 0;
        margin-bottom: 32px;
        @include page-title-two;
    }
    &__head {
        display: flex;
        justify-content: space-between;
        align-items: start;

        a {
            font-size: 18px;
        }
    }
    &__list {
        display: flex;
        gap: 20px;
    }
    .article {
        background-color: #fff;
    }
}
@media(max-width:992px){
    .last-articles__list {
        flex-wrap: wrap;
    }
    .last-articles .article {
        width: calc(33% - 12px);
    }
}
@media(max-width:975px){
    .last-articles__title {
        font-size: 34px;
    }
}
@media(max-width:768px){
    .last-articles .article {
        width: calc(50% - 10px);
    }
    .last-articles__title {
        font-size: 28px;
    }
}
@media(max-width:480px){
    .last-articles .article {
        width: 100%;
    }
    .last-articles__title {
        font-size: 26px;
    }
    .last-articles {
        padding: 30px 0;
    }
}
@media(max-width:390px){
    .last-articles__head {
        flex-direction: column;
    }
    .last-articles__title {
        margin-bottom: 12px;
    }
    .last-articles__head a {
        width: 100%;
        text-align: center;
    }
}
@media(max-width: 380px){
    .last-articles__title {
        font-size: 24px;
    }
}