.support {
    padding-bottom: 120px;

    &__title {
        @include page-title-one;
    }

    &__content {
        font-size: 16px;
        color: $black-color;
        p {
            margin-bottom: 20px;
        }
    }
}
.callback-form {
    background-color: $grey-color;
    padding: 50px 0;

    &__title {
        @include page-title-two;
        margin-bottom: 40px;
    }
    &__wrapper {
        width: 699px;
    }
    .container {
        display: flex;
        column-gap: 99px;
    }
    form {
        display: flex;
        flex-direction: column;
    }
    &__input {
        background-color: transparent;
        border: none;
        height: 45px;
        font-size: 24px;
        line-height: 120%;
        border-bottom: 1px solid #abababa1;
        outline: none;
        margin-bottom: 25px;
        color: $black-color;

        &::placeholder {
            font-size: 18px;
            color: #DADADA;
            text-transform: uppercase;
            font-weight: 400;
        }
        &:hover,
        &:focus {
            border-color: $pink-color;
        }
    }
    &__comment {
        background-color: transparent;
        border: none;
        height: 120px;
        font-size: 24px;
        line-height: 120%;
        border-bottom: 1px solid #abababa1;
        outline: none;
        margin-bottom: 25px;
        resize: none;
        color: $black-color;

        &::placeholder {
            color: #DADADA;
            text-transform: uppercase;
            font-weight: 400;
            font-size: 18px;
        }
        &:hover,
        &:focus {
            border-color: $pink-color;
        }
    }
    &__aggr {
        

        label {
            position: relative;
            padding-left: 45px;
            height: 30px;
            display: flex;
            align-items: center;
            margin-bottom: 35px;
            &::before {
                content: "";
                width: 30px;
                height: 30px;
                display: inline-block;
                background-color: transparent;
                border: 1px solid #abababa1;
                position: absolute;
                border-radius: 6px;
                left: 0;
                cursor: pointer;
            }
            &::after {
                content: "";
                display: none;
                width: 46px;
                height: 40px;
                background-image: url("../img/icons/pink-galochka.png");
                background-repeat: no-repeat;
                background-size: contain;
                position: absolute;
                left: 2px;
                top: -8px;
                cursor: pointer;
            }

            span {
                text-transform: uppercase;
                font-weight: 500;
            }
        }
    }
    &__check {
        display: none;
    }
    &__submit {
        width: auto;
        border: none;
        outline: none;
        font-size: 18px;
        color: #fff;
        font-weight: 500;
        padding: 15px 30px;
        max-width: 250px;
        transition: .4s;
        cursor: pointer;

    }
    &__form-img {
        padding-top: 50px;
    }
}
.callback-form__check:checked + label::after {
    display: block;
}
@media(max-width:1199px){
    .callback-form .container {
        margin: 0 20px;
        width: auto;
        column-gap: 10px;
        justify-content: space-between;
    }
    .support {
        padding-bottom: 100px;
    }
}
@media(max-width: 1024px){
    .callback-form__title {
        font-size: 36px;
    }
    .support__title {
        font-size: 46px;
        margin-bottom: 35px;
    }
}
@media(max-width:991px){
    .callback-form .container {
        align-items: center;
    }
    .callback-form__form-img {
        padding-top: 0;
    }
}
@media(max-width: 967px){
    .callback-form__title {
        font-size: 34px;
    }
    .support__title {
        font-size: 42px;
        margin-bottom: 25px;
    }
    .support {
        padding-bottom: 80px;
    }
}
@media(max-width: 860px){
    .callback-form__title {
        font-size: 32px;
    }
    .support__title {
        font-size: 38px;
        margin-bottom: 20px;
    }
}
@media(max-width:767px){
    .support {
        padding-bottom: 50px;
    }
    .callback-form {
        padding-top: 35px;
    }
}
@media(max-width:830px){
    .callback-form .container {
        flex-direction: column;
        row-gap: 30px;
    }
    .callback-form__wrapper {
        width: 100%;
    }
}
@media(max-width: 630px){
    .callback-form__title {
        font-size: 28px;
    }
    .support__title {
        font-size: 34px;
    }
}
@media(max-width: 585px){

    .support__title {
        font-size: 30px;
    }
}
@media(max-width: 480px){
    .callback-form__title {
        font-size: 26px;
    }
    .support__title {
        font-size: 28px;
    }
    .callback-form__submit {
        max-width: 100%;
    }
    .support {
        padding-bottom: 30px;
    }
    .callback-form {
        padding-top: 25px;
    }
}
@media(max-width: 360px){
    .callback-form__title {
        font-size: 24px;
    }
    .support__title {
        font-size: 26px;
    }
}
@media(max-width: 330px){
    .callback-form__title {
        font-size: 22px;
    }
    .support__title {
        font-size: 24px;
    }
}