.strimers {
    &__title {
        @include page-title-one;
    }
    &__info {
        font-size: 20px;
        line-height: 110%;
        font-weight: 300;
        color: #535353;
    }
    padding-bottom: 120px;
}
.strimer {
    
    &__top {
        display: flex;
        column-gap: 50px;
        min-height: 500px;
        margin-bottom: 50px;

        &.reverse {
            flex-direction: row-reverse;
            justify-content: space-between;
        }
    }
    &__image {
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        width: 50%;
        border-radius: 12px;
    }
    &__name {
        @include page-title-two;
        margin-bottom: 50px;
    }
    &__socail-list {
        position: relative;

        li {
            font-size: 20px;
            line-height: 115%;
            text-transform: uppercase;
            padding-left: 45px;
            font-weight: 300;
            margin-bottom: 30px;
        }
        a {
            color: #7b78a8;
            transition: $transition;
            border-bottom: 1px solid #7b78a8;

            &:hover {
                color: $pink-color;
                border-color: $pink-color;
            }
        }

        li::before {
            content: "";
            width: 30px;
            height: 30px;
            display: block;
            background-image: url("../img/icons/list-shiny-dot.png");
            background-repeat: no-repeat;
            background-size: contain;
            position: absolute;
            left: 0;
        }
    }
    &__bottom {
        padding: 45px 35px 55px 35px;
        background-color: $grey-color;
        margin-bottom: 60px;
        border-radius: 12px;

        p {
            font-size: 20px;
            line-height: 110%;
            font-weight: 300;
            color: $black-color;
        }
    }
}
@media(max-width:1199px){
    .strimers {
        margin-top: 28px;
    }
}
@media(max-width: 1024px){
    .strimers__title {
        font-size: 46px;
        margin-top: 0;
        margin-bottom: 35px;
    }
    .strimer__name {
        font-size: 36px;
    }
    .strimer__socail-list li {
        font-size: 26px;
    }
}
@media(max-width: 967px){
    .strimers__title {
        font-size: 42px;
        margin-bottom: 25px;
    }
    .strimer__name {
        font-size: 34px;
        margin-bottom: 30px;
    }
    .strimer__top {
        column-gap: 40px;
        min-height: 400px;
        margin-bottom: 40px;
    }
}
@media(max-width: 860px){
    .strimers__title {
        font-size: 38px;
        margin-bottom: 20px;
    }
    .strimer__name {
        font-size: 32px;
    }
    .strimer__socail-list li {
        font-size: 22px;
        margin-bottom: 25px;
    }
    .strimer__socail-list li::before {
        width: 34px;
        height: 34px;
    }
    .strimer__bottom {
        padding: 35px;
    }
    .strimer__bottom p,
    .strimers__info {
        font-size: 18px;
    }
}
@media(max-width: 630px){
    .strimers__title {
        font-size: 34px;
    }
    .strimer__name {
        font-size: 30px;
    }
    .strimer__top {
        column-gap: 30px;
        min-height: 300px;
    }
    .strimer__bottom {
        padding: 25px;
        margin-bottom: 50px;
    }
    .strimer__bottom p,
    .strimers__info {
        font-size: 16px;
    }
}
@media(max-width: 585px){
    .strimers__title {
        font-size: 30px;
    }
    .strimer__name {
        font-size: 26px;
    }
    .strimer__socail-list li {
        font-size: 18px;
        margin-bottom: 20px;
        padding-left: 35px;
    }
    .strimer__socail-list li::before {
        width: 24px;
        height: 24px;
    }
}
@media(max-width:525px){
    .strimer__socail-list li {
        padding-left: 34px;
    }
    .strimer__socail-list li::before {
        width: 28px;
        height: 28px;
    }
    .strimer__top {
        column-gap: 20px;
    }
}
@media(max-width: 480px){
    .strimers__title {
        font-size: 28px;
    }
    .strimer__name {
        font-size: 24px;
    }
    .strimer__bottom p,
    .strimers__info {
        font-size: 14px;
    }
    .strimer__top,
    .strimer__top.reverse {
        flex-direction: column;
        row-gap: 10px;
    }
    .strimer__image {
        width: 100%;
        height: 260px;
    }
    .strimer__name {
        margin-bottom: 20px;
    }
    .strimer__socail-list li {
        margin-bottom: 20px;
    }
    .strimer__socail-list li::before {
        width: 24px;
        height: 24px;
    }
    .strimer__top {
        margin-bottom: 20px;
    }
    .strimers {
        padding-bottom: 35px;
    }
}
@media(max-width:400px){
    .strimer__bottom {
        padding: 15px;
        margin-bottom: 30px;
    }
}
@media(max-width: 360px){
    .strimers__title {
        font-size: 26px;
    }
    .strimer__name {
        font-size: 22px;
    }
    .strimers {
        margin-top: 15px;
        padding-bottom: 25px;
    }
}
@media(max-width: 330px){
    .strimers__title {
        font-size: 24px;
    }
    .strimer__name {
        font-size: 20px;
    }
}