.modal-login {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    display: none;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.631372549);
    z-index: 20;

    &__body {
        background-color: #fff;
        padding: 45px 30px 30px;
        width: 860px;
        position: relative;
        border-radius: 12px;
    }
    &__title {
        @include page-title-two;
        margin-bottom: 30px;
    }
    &__subtitle {
        font-size: 18px;
        margin-bottom: 30px;
    }
    &__close {
        width: 40px;
        height: 40px;
        background-image: url("../img/icons/icon-close.png");
        background-repeat: no-repeat;
        background-size: contain;
        right: 10px;
        top: 10px;
        background-position: center;
        position: absolute;
        cursor: pointer;
    }
    &__wrapper {
        display: flex;
        justify-content: space-between;
        align-items: end;
        column-gap: 30px;

        .modal-form-input {
            width: calc(50% - 15px);
        }
    }

    &__submit {
        border: none;
        outline: none;
        padding: 15px 45px;
        font-size: 18px;
        font-weight: 400;
        color: #fff;
        transition: .4s;
        cursor: pointer;
    }
}
.modal-form-input {
    border: none;
    outline: none;
    background-color: transparent;
    border-bottom: 1px solid #ABABAB;
    font-size: 18px;
    padding: 10px 5px;
    margin-bottom: 20px;
    font-weight: 300;
    transition: .4s;

    &::placeholder {
        font-weight: 200;
        font-size: 18px;
    }
    &:focus {
        border-color: $pink-color;
    }
}
.modal-open-forgot {
    text-transform: uppercase;
    font-size: 16px;
    border-bottom: 1px solid $pink-color;
}
@media(max-width:900px){
    .modal-login__body {
        margin: 0 12px;
    }
}
@media(max-width:578px){
    .modal-login__wrapper {
        flex-direction: column;
        align-items: start;
    }
    .modal-login__wrapper .modal-form-input {
        width: 100%;
    }
    .modal-login__wrapper--btns {
        flex-direction: column-reverse;
    }
    .modal-login__submit {
        width: 100%;
    }
}
@media(max-width:480px){
    .modal-login__title {
        font-size: 32px;
    }
    .modal-login__close {
        width: 30px;
        height: 30px;
    }
}
@media(max-width:390px){
    .modal-login__title {
        font-size: 28px;
        margin-bottom: 16px;
    }
    .modal-login__subtitle {
        font-size: 16px;
    }
    .modal-login__body {
        padding: 45px 15px 30px;
    }
}
@media(max-width:370px){
    .modal-login__title {
        font-size: 24px;
    }
    .modal-login__subtitle {
        font-size: 14px;
    }
}