.article-detail {
    padding-bottom: 80px;

    &__title {
        @include page-title-one;
    }
    &__date {
        font-weight: 600;
        font-size: 16px;
    }
    &__banner {
        img {
            max-height: 500px;
            width: 100%;
            object-fit: cover;
            border-radius: 12px;
        }
    }
    &__info {
        margin-top: 20px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 20px;
    }
    &__tag {
        padding: 6px 20px;
        font-size: 16px;
        background-color: $black-color;
        color: #fff;
        border: 1px solid $black-color;
        transition: .4s;
        border-radius: 6px;
        &:hover {
            background-color: #fff;
            color: $black-color;
        }
    }
    &__container {
        display: flex;
        gap: 32px;
    }
    &__content {
        max-width: 800px;

        &--news {
            max-width: 100%;
        }
    }
    &__table-content {
        width: 320px;

        &-wrapper {
            padding: 24px 16px;
            background-color: $grey-color;
            border-radius: 12px;
            position: sticky;
            top: 145px;
        }
        &-title {
            font-weight: 700;
        }
        &-list {
            padding-left: 25px;
            list-style: unset;

            li {
                padding-left: 0;
                list-style: auto;

                a {
                    color: $purple-color;
                }
            }
        }
    }
    &__text-block {
        margin: 32px 0;

        &:first-child {
            margin-top: 0;
        }
        &:last-child {
            margin-bottom: 0;
        }
    }
    &__text-block ul,
    &__text-block ol {
        padding-left: 25px;
        margin-bottom: 16px;
    }
    &__text-block ol {
        list-style: auto;
    }
    &__text-block ul {
        list-style: disc;
    }
    &__text-block h2,
    &__text-block h3,
    &__text-block h4 {
        margin-top: 0;
    }
    &__text-block li {
        list-style: unset;
        padding-left: 0;
        margin-bottom: 12px;
    }
    &__text-block img {
        object-fit: cover;
        border-radius: 12px;
        width: 100%;
    }
    &__slider {
        margin: 32px 0;
        overflow-x: hidden;
        width: 100%;
        position: relative;

        &--news {
            margin: 32px 0;
            overflow-x: hidden;
            width: 100%;
            position: relative;
        }

        &-controls {
            display: flex;
            width: 100%;
            justify-content: space-between;
            position: absolute;
            top: calc(50% - 20px);
        }
        &-next {
            margin-right: 8px;
            z-index: 19;
        }
        &-prev {
            margin-left: 8px;
            z-index: 19;
        }
        &:first-child {
            margin-top: 0;
        }
        &:last-child {
            margin-bottom: 0;
        }
    }

    &__slide img {
        border-radius: 12px;
        object-fit: cover;
        width: 100%;
    }
}
@media(max-width:1199px){
    .article-detail {
        margin-top: 28px;
    }
    .article-detail__content:not(.article-detail__content--news) {
        max-width: 700px;
    }
    .article-detail__table-content {
        width: 100%;
    }
}
@media(max-width: 1024px){
    .article-detail__title {
        font-size: 46px;
        margin-bottom: 35px;
    }
    .article-detail__content:not(.article-detail__content--news) {
        max-width: 650px;
    }
}
@media(max-width: 967px){
    .article-detail__title {
        font-size: 42px;
        margin-bottom: 25px;
    }
    .article-detail__content:not(.article-detail__content--news) {
        max-width: 600px;
    }
}
@media(max-width:920px){
    .article-detail__content:not(.article-detail__content--news) {
        max-width: 550px;
    }
}
@media(max-width: 860px){
    .article-detail__title {
        font-size: 38px;
        margin-bottom: 20px;
    }
}
@media(max-width:830px){
    .article-detail__content:not(.article-detail__content--news) {
        max-width: 500px;
    }
}
@media(max-width:768px){
    .article-detail {
        margin-top: 15px;
        padding-bottom: 30px;
    }
    .article-detail__content:not(.article-detail__content--news) {
        max-width: 400px;
    }
}
@media(max-width:678px){
    .article-detail__container {
        flex-direction: column-reverse;
    }
    .article-detail__content {
        max-width: 100%;
    }
}
@media(max-width: 630px){
    .article-detail__title {
        font-size: 34px;
    }
}
@media(max-width: 585px){
    .article-detail__title {
        font-size: 30px;
    }
}
@media(max-width: 480px){
    .article-detail__title {
        font-size: 28px;
    }
    .article-detail__slider-prev,
    .article-detail__slider-next {
        width: 24px;
        height: 24px;
    }
}
@media(max-width: 360px){
    .article-detail__title {
        font-size: 26px;
    }
}
@media(max-width: 330px){
    .article-detail__title {
        font-size: 24px;
    }
}